import { Img_Contact_QR1, Img_Contact_QR2 } from '../../images/ContactQr';
import { Sub_Bg_Img } from '../../images/SubBg';
import './AboutUs.scss';

export const AboutUs = () => {
    return (
        <div className="AboutUsPage">
            <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
                <div className="headContent">
                    <div className="title" style={{ textAlign: "center" }}>关于我们</div>
                    <div className="subTitle">Oli Capital Pty Ltd 成立于2018年，是一家位于悉尼的金融服务和基金管理公司。</div>
                    <div className="subTitle">我们专注于一级和二级市场的融资、经纪服务和基金管理。我们的基金管理团队在全球主要交易所的上市股权投资方面拥有丰富的经验。</div>

                </div>
            </header>
            <div className="info">

                <div className="contact">
                    <div className="contactTitle">
                        联系我们
                    </div>
                    <div className="content">
                        <div className="info-wrapper">

                            <div className="subTitle">地址</div>
                            <div className='item'>Suite 1103, 66 Goulburn St
                                Sydney NSW 2000</div>
                        </div>

                        <div className="info-wrapper">
                            <div className="subTitle">邮箱</div>
                            <div className='item'>info@olicapital.com.au</div>
                        </div>
                        <div className="info-wrapper">
                            <div className="subTitle">电话</div>
                            <div className='item'> (02) 8288 9086</div>
                        </div>
                        <div className="info-wrapper">
                            <div className="subTitle">微信</div>
                            <div className='item'> 微信 (扫描下方二维码)</div>
                        </div>
                        <div className="wechat">
                            <div className="customer">
                                <img src={Img_Contact_QR1} alt="" />
                                <div>客服号</div>
                            </div>
                            <div className="customer">
                                <img src={Img_Contact_QR2} alt="" />
                                <div>微信公众号
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}