import { Ajax } from '../utils/http';
import { LocalStorageService } from '../utils/storage';

export class LoginService {
    private email: string;
    private password: string;
    private validityday: number;
    constructor(_emal: string, _password: string, _validityday: number) {
        this.email = _emal;
        this.password = _password;
        this.validityday = _validityday;
    }

    public async send(): Promise<UserInfo> {
        let response: UserInfo;
        let data = {
            email: this.email,
            password: this.password,
            validityday: this.validityday
        }
        try {
            response = await new Ajax<UserInfo>('/login/emailLogin', data).post();
        } catch (e) {
            throw e;
        }
        LocalStorageService.set('token', response.token);
        return response;
    }
}
export class RegisterService {
    private data: any;
    constructor(_data: any) {
        this.data = _data;
    }

    public async send() {
       
        try {
            await new Ajax('/login/register', this.data).post();
        } catch (e) {
            throw e;
        }
    }
}

