import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { Sub_Bg_Img } from '../../images/SubBg';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-calendar/dist/Calendar.css';
import './Kline.scss';
import NoData from '../Stock/NoData';
import { klineStore } from '../../stores/KlineStore';
import { StrategiesMap } from '../../services/StatisticService';
import { verifyRule } from '../../utils/globalFuncs';

export const Kline = observer(() => {
  useEffect(() => {
    klineStore.getData();
  }, []);
  const tabs = [{ text: "牛", id: 0, type: 'bull' },
  { text: "中", id: 1, type: "neutral" },
  { text: "熊", id: 2, type: "bear" }]
  return (
    <div className="klineWrapper">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title" style={{textAlign:"center"}}>K线形态</div>
          <div className="subTitle">为广大投资者提供对澳洲资本市场的独到见解、深度分析</div>
        </div>
      </header>
      <div className="klineContent">
        <div className="topNav">
          <div className="menu">
            {
              tabs.map((r) => {
                return <div className="title" style={{ color: klineStore.type === r.type ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                  klineStore.type = r.type;
                }}>
                  {r.text}
                </div>
              })
            }

          </div>
        </div>
        <div className="klineList">
          {klineStore.loading ? <div className="loading">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
          </div> : (klineStore.list.filter((l: any) => l.trend == klineStore.type)).map((n, i) => {
            var strategy = StrategiesMap[n.pattern_name.en] || {};
            return <div key={i} className="klineItem left" onClick={()=>{
            verifyRule('technicalForm',`/klineDetail/${n.pattern_name.en}`)
            }}>
              <div className="tit-pic ">
                {strategy && <img src={strategy['icon']} />}
              </div>
              <div className="content" >
                <div className="title">{n.pattern_name.ch}.</div>
                <div className="info"> {n.description.ch}</div>
              </div>

            </div>
          })}

        </div>


      </div>
    </div>
  )
});