import './Publish.scss';
import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import { Input, Spin } from 'antd';
import { ApplyPage } from './Apply';
import { userStore } from '../../stores/UserStore';
import Dragger from 'antd/lib/upload/Dragger';
import { settingStore } from '../../stores/SettingStore';
import { Uploader } from './Uploader';
import { VideoEditItem } from './VideoEditItem';
import { publishStore, VideoUploadItem } from '../../stores/PublishStore';
import { VideoCollectModal } from './VideoCollection';

export const PublishVideoPage = observer(() => {
    useEffect(()=>{
        publishStore.getCollections();
    },[]
    )
    const beforeUpload = (file: any) => {
        var items = JSON.parse(JSON.stringify(publishStore.videoUploadItems));
        var names = items.map((i:VideoUploadItem) => i.name);
        if(names.indexOf(file.name)== -1){
            items.push({
                file:file,
                name:file.name,
            })
            if (items.length == 1) {
                publishStore.currentVideoId = file.name;
            }
            publishStore.videoUploadItems = items;
        }
        return false;
    }
    const props = {
        name: 'file',
        multiple: true,
        beforeUpload: beforeUpload,
        itemRender: (originNode: any, file: any, currFileList: any) => {
            return (
            <div></div>
        )}

    };

    return (
        <div className="publishVideoWrapper">
            <div className="headerTop">
            </div>
            {userStore.userInfo?.identity == 'creator' ? <div>
                <div className="garr-video-container upload-video">
                    <div className="byte-tabs byte-tabs-horizontal byte-tabs-line byte-tabs-size-default xigua-upload-manage">
                        <div className="byte-tabs-header-nav byte-tabs-header-nav-horizontal byte-tabs-header-size-default byte-tabs-header-nav-line">
                            <div className="byte-tabs-header-scroll is-dropdown">
                                <div className="byte-tabs-header-wrapper">
                                    <div className="byte-tabs-header" >
                                        <div className={`byte-tabs-header-title ${publishStore.isCollect?'':'active'}`} onClick={()=>{
                                            publishStore.collectModalVisible=false;
                                        }}>发布视频</div>
                                        <div className={`byte-tabs-header-title ${publishStore.isCollect?'active':''}`} onClick={()=>{
                                            publishStore.collectModalVisible=true;
                                        }}>创建合集</div>
                                        <div className="byte-tabs-header-ink" >
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div>
                            
                        </div>
      
                        <div className="byte-tabs-content byte-tabs-content-horizontal">
                            <div className="byte-tabs-content-inner" >
                                <div className="byte-tabs-content-item byte-tabs-content-item-active">
                                    <div className="byte-tabs-pane">
                                        <div className="xigua-upload-video">
                                            <div className="xigua-upload-video-empty">
                                                <a className="xg_play_banner" target="_blank" rel="noopener referrer" >
                                                    <img src="//lf3-static.bytednsdoc.com/obj/eden-cn/nuhpephips/post-banner.png" alt="" />
                                                </a>
                                                <div className="tip-container">
                                                </div>
                                                <div className="upload-video-trigger">
                                                    <div className="byte-upload xigua-upload-video-trigger upload-video-trigger-card no-tip">
                                                        <div className="byte-upload-trigger">
                                                            <div className="byte-upload-trigger-drag">
                                                                <div className="byte-upload-trigger-area">
                                                                    <div className="byte-upload-trigger-tip">
                                                                        <div className="upload-tip-container">

                                                                            <Dragger {...props}>
                                                                                <div className="trigger-tip-icon">
                                                                                    <svg width="88" height="72" fill="none">
                                                                                        <path fill-rule="evenodd" clip-rule="evenodd" d="M44.273 0c14.188 0 25.69 11.569 25.69 25.84 0 3.203-.58 6.27-1.64 9.1v32.132H53.018a3.29 3.29 0 01-3.193-3.15l-.004-.149h-1.175v3.298h-7.653v-3.298h-2.038a3.29 3.29 0 01-3.28 3.299l-.073-.001-.074-.003v.003H19.676v-.016C8.737 66.622 0 57.565 0 46.455c0-10.737 8.161-19.557 18.584-20.527v-.089C18.584 11.57 30.085 0 44.274 0z" fill="url(#new-upload-icon_inline_svg__paint0_linear)">
                                                                                        </path>
                                                                                        <path fill-rule="evenodd" clip-rule="evenodd" d="M67.503 25.84C78.823 25.84 88 35.07 88 46.454c0 11.387-9.177 20.617-20.497 20.617H53.018a3.29 3.29 0 01-3.193-3.15l-.004-.149h-2.815V46.455c0-11.272 8.994-20.431 20.158-20.613l.34-.003z" fill="url(#new-upload-icon_inline_svg__paint1_linear)">
                                                                                        </path>
                                                                                        <path fill-rule="evenodd" clip-rule="evenodd" d="M28.205 45.358l12.354-12.345.025-.025.028-.028a5.378 5.378 0 013.721-1.58 5.38 5.38 0 013.907 1.58l.028.028.025.025 12.354 12.345.025.025a5.433 5.433 0 01-.025 7.652l-.028.029a5.39 5.39 0 01-7.627 0l-3.17-3.168v16.661c0 3.006-2.423 5.443-5.412 5.443h-.044c-2.988 0-5.41-2.437-5.41-5.443V49.971l-3.095 3.093a5.39 5.39 0 01-7.628 0l-.028-.029a5.433 5.433 0 01-.025-7.652l.025-.025z" fill="#FAFAFA">
                                                                                        </path>
                                                                                        <defs>
                                                                                            <linearGradient id="new-upload-icon_inline_svg__paint0_linear" x1="60.316" y1="7.259" x2="13.793" y2="62.494" gradientUnits="userSpaceOnUse">
                                                                                                <stop stop-color="#E7E7E7">
                                                                                                </stop>
                                                                                                <stop offset="1" stop-color="#F9F8F9">
                                                                                                </stop>
                                                                                            </linearGradient>
                                                                                            <linearGradient id="new-upload-icon_inline_svg__paint1_linear" x1="78.695" y1="29.071" x2="49.917" y2="66.911" gradientUnits="userSpaceOnUse">
                                                                                                <stop stop-color="#E6E6E6">
                                                                                                </stop>
                                                                                                <stop offset="1" stop-color="#F0EFF1">
                                                                                                </stop>
                                                                                            </linearGradient>
                                                                                        </defs>
                                                                                    </svg>
                                                                                </div>
                                                                                <div className="text-tips">
                                                                                    <div className="upload-tip">点击上传或将文件拖入此区域</div>
                                                                                </div>
                                                                            </Dragger>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="uploader-item">
                                                    {publishStore.videoUploadItems.map((item)=>{
                                                        return (<div onClick={() => {
              
                                                          publishStore.pickItem(item);

                                                        }}  className={publishStore.currentVideoId ==  item.name?"pick-active": ""}>
                                                            <Uploader
                                                                item={item}
                                                            />
                                                        </div>)
                                                    })}
                                                </div>
                                                {publishStore.videoUploadItems.length > 0 ?
                                                    <div className="list-wrapper">
                                                        {publishStore.videoUploadItems.map((item, i) => {
                                                            return <div className={publishStore.currentVideoId == item.name?"active": ""}>
                                                                <VideoEditItem item={item} index={i} />
                                                            </div>
                                                        })}
                                                    </div>
                                                    : <div className="stage-footer">
                                                        <div className="recommend-block">
                                                            <svg width="40" height="40" className="icon">
                                                                <g fill="none" fill-rule="evenodd">
                                                                    <path fill="#E6E6E6" d="M8 5h24c2.21 0 4 1.79 4 4v22c0 2.21-1.79 4-4 4H8c-2.21 0-4-1.79-4-4V9c0-2.21 1.79-4 4-4z">
                                                                    </path>
                                                                    <circle cx="29" cy="12" r="3" fill="#FFF">
                                                                    </circle>
                                                                    <path fill="#FFF" d="M6.993 29.459l8.24-12.796a2 2 0 013.382.031l4.114 6.644a.499.499 0 00.838.02l1.098-1.606a1.848 1.848 0 013.056.005l5.218 7.681A1.001 1.001 0 0132.112 31H7.833a1 1 0 01-.84-1.541z">
                                                                    </path>
                                                                </g>
                                                            </svg>
                                                            <div className="recommend-msg">
                                                                <div className="title">推荐尺寸</div>
                                                                <div className="tip">16:9、18:9、21:9 的横版视频</div>
                                                            </div>
                                                        </div>
                                                        <div className="recommend-block">
                                                            <svg width="40" height="40" className="icon">
                                                                <g fill="none" fill-rule="evenodd">
                                                                    <path fill="#E6E6E6" d="M7 5h26c2.21 0 4 1.79 4 4v22c0 2.21-1.79 4-4 4H7c-2.21 0-4-1.79-4-4V9c0-2.21 1.79-4 4-4z">
                                                                    </path>
                                                                    <path fill="#FFF" d="M7 11h26a2 2 0 012 2v14a2 2 0 01-2 2H7a2 2 0 01-2-2V13a2 2 0 012-2z">
                                                                    </path>
                                                                </g>
                                                            </svg>
                                                            <div className="recommend-msg">
                                                                <div className="title">推荐分辨率</div>
                                                                <div className="tip">≥ 1920*1080</div>
                                                            </div>
                                                        </div>
                                                        <div className="recommend-block">
                                                            <svg width="40" height="40" className="icon">
                                                                <g fill="none" fill-rule="evenodd">
                                                                    <path fill="#E6E6E6" d="M6 8h19c2.21 0 4 1.79 4 4v16c0 2.21-1.79 4-4 4H6c-2.21 0-4-1.79-4-4V12c0-2.21 1.79-4 4-4z">
                                                                    </path>
                                                                    <path fill="#FFF" fill-rule="nonzero" d="M11.46 15c1.509 0 2.808.579 3.667 1.558l-1.123 1.11c-.595-.725-1.489-1.172-2.544-1.172-1.935 0-3.356 1.426-3.356 3.507 0 2.082 1.42 3.508 3.288 3.508 1.726 0 2.74-.91 2.984-2.501h-2.51v-1.448h4.168c.007.124.014.248.014.373 0 2.77-1.726 5.065-4.656 5.065C8.95 25 6.5 23.222 6.5 20.003 6.5 17.102 8.584 15 11.46 15zm5.604.18h3.674c2.023 0 3.545.709 3.545 2.625 0 .992-.629 1.82-1.461 2.054.9.268 1.678 1.102 1.678 2.212 0 1.923-1.387 2.757-3.546 2.757h-3.89v-9.649zm3.823 5.513h-2.294v2.674h2.294c1.204 0 2.01-.235 2.01-1.351 0-1.09-.806-1.323-2.01-1.323zm-.21-4.053h-2.084v2.598h2.097c1.063 0 1.99-.165 1.99-1.316 0-1.11-.94-1.282-2.003-1.282z">
                                                                    </path>
                                                                    <path fill="#E6E6E6" d="M31 14.571l5.453-3.56a.998.998 0 011.547.836v16.306a1 1 0 01-1.547.837L31 25.429V14.57z">
                                                                    </path>
                                                                </g>
                                                            </svg>
                                                            <div className="recommend-msg">
                                                                <div className="title">视频大小</div>
                                                                <div className="tip">≤ 32G</div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                }</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="byte-tabs-content-item">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>





            </div> : <ApplyPage />}
            <VideoCollectModal visible={publishStore.collectModalVisible} cancle={() => { publishStore.collectModalVisible = false }} />

        </div>
    )
});