import './Publish.scss';
import { observer } from 'mobx-react';
import { publishStore, VideoUploadItem } from '../../stores/PublishStore';

import { Select } from 'antd';
import { CollectionItem } from '../../services/CollectionListService';
import { ImageUpload } from './ImageUpload';

const { Option } = Select;

export interface VideoEditItemProps {
    index: number,
    item: VideoUploadItem,
}

export const VideoEditItem = observer((props: VideoEditItemProps) => {
    console.log("props",props.item)
    return (
        <div className="video-form-bone video-form-basic" style={{ width: '100%', position: 'absolute', zIndex: publishStore.currentVideoId == props.item.name ? 1 : -1 }}>
            <div className="video-form-wrapper">
                <div className="video-form-item form-item-title">
                    <div className="video-form-item-label required">
                        <span>标题</span>
                    </div>
                    <div className="video-form-item-wrapper">
                        <div className="video-form-item-control">
                            <div className="article-title-wrap">
                                <div className="xigua-input-wrapper">
                                    <input placeholder="请输入 1～30 个字符" value={props.item.title || ''} onChange={(v) => {
                                        let value = v.target.value;
                                        props.item.title = value;
                                    }} className="xigua-input show-limit byte-input byte-input-size-large" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="video-form-item form-item-title">
                    <div className="video-form-item-label required">
                        <span>合集</span>
                    </div>
                    <div className="video-form-item-wrapper">
                        <div className="video-form-item-control">
                            <div className="article-title-wrap">
                                <div className="xigua-input-wrapper">

                                    <Select
                                        showSearch
                                        placeholder="Search to Select"
                                        optionFilterProp="children"
                                        onChange={(v) => {
                                            props.item.collectid = `${v}`;
                                            props.item.price = '';
                                        }}

                                        value={props.item.collectid || ''}
                                        filterOption={(input, option: any) =>
                                            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                        }
                                        filterSort={(optionA, optionB) =>
                                            optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())
                                        }
                                    >
                                        {publishStore.collections.map((c: CollectionItem) => <Option value={c.id}>{c.name}</Option>)}
                                    </Select>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {!props.item.collectid &&<div className="video-form-item form-item-title">
                    <div className="video-form-item-label required">
                        <span>价格</span>
                    </div>
                    <div className="video-form-item-wrapper">
                        <div className="video-form-item-control">
                            <div className="article-title-wrap">
                                <div className="xigua-input-wrapper">
                                    <input placeholder="请输入该视频的价格" value={props.item.price || ''} onChange={(v) => {
                                        let value = v.target.value;
                                        props.item.price = value;
                                    }} className="xigua-input show-limit byte-input byte-input-size-large" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>}

                <div className="video-form-item form-item-poster">
                    <div className="video-form-item-label required">
                        <span>封面</span>
                    </div>
                    <div className="video-form-item-wrapper">
                        <div className="video-form-item-control">
                            <div className="item">
                                <div className="xigua-poster-editor">
                                    <div className="fake-upload-trigger">
                                        <div>
                                            <ImageUpload callback={(url: string) => {
                                            props.item.thumb =url;
                                        }} />
                                            <div className="trigger-tip">上传封面</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="video-form-item form-item-abstract">
                    <div className="video-form-item-label" >
                        <span>视频简介</span>
                    </div>
                    <div className="video-form-item-wrapper">
                        <div className="video-form-item-control" >
                            <div className="byte-textarea-wrapper">

                                <textarea value={props.item.abstract || ''} onChange={(v) => {
                                    let value = v.target.value;
                                    props.item.abstract = value;
                                }} className="byte-textarea abstract" placeholder="请输入视频简介">
                                </textarea>
                            </div>
                        </div>
                    </div>
                </div>

                <button className="publish-button " onClick={() => {
                    publishStore.publicVideo(props.item.name);
                }}><span
                    className="_1Qjd7">发布</span></button>
            </div>
        </div>
    )
});