
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import { Icon_Play } from '../../images/icons/Play';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import './CreatorVideos.scss';
import NoData from '../Stock/NoData';
import { EditVideoModal } from './EditVideo';
import { CreatorVideoItem } from '../../services/CreatorVideoService';
import { StripeModal } from '../Stripe/Stripe';
import { stripeStore } from '../../stores/StripeStore';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import { LazyLoadImage } from 'react-lazy-load-image-component';

export const VideoListPage = observer((props: any) => {
    useEffect(() => {
        creatorVideoStore.getData();
        creatorVideoStore.getVideoCount();
    }, []);
    var isCharge: boolean = !creatorVideoStore.isfree;
    return <div className="videoWrapper">
        <div className="price-tabs">
            <div className="item" style={{ backgroundColor: isCharge ? '#3272a2' : '#ffffff', color: isCharge ? '#ffffff' : '#3272a2' }} onClick={() => {
                creatorVideoStore.isfree = false;
                creatorVideoStore.getData();
            }}>
                <img src="" alt="" />
                <div className="icon" style={{ borderColor: !isCharge ? '#3272a2' : '#ffffff' }}>￥</div>
                <div className="text">付费视频</div>
                <div className="amount">{creatorVideoStore.videoCount?.payCount || '0'}</div>
            </div>
            <div className="item" style={{ backgroundColor: !isCharge ? '#3272a2' : '#ffffff', color: !isCharge ? '#ffffff' : '#3272a2' }} onClick={() => {
                creatorVideoStore.isfree = true;
                creatorVideoStore.getData();
            }}>
                <img src="" alt="" />
                <div className="icon" style={{ borderColor: isCharge ? '#3272a2' : '#ffffff' }}>￥</div>
                <div className="text">免费视频</div>
                <div className="amount">{creatorVideoStore.videoCount?.freeCount || '0'}</div>
            </div>
        </div>

        <div>
            {creatorVideoStore.isSelf && <div className="editWrapper">
                <div className="edit" onClick={() => { creatorVideoStore.isEditing = !creatorVideoStore.isEditing }}>编辑</div>
            </div>}
            <ul className="collectionDetailContent">
                {creatorVideoStore.loading ? <div className="loading">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                </div> : creatorVideoStore.list.length == 0 ? <NoData /> : creatorVideoStore.list.map((item, idx) => {
                    let needBuy = item.lookauth != 1 && parseFloat(item.price) > 0
                    return <li key={idx} role="listitem" className="videoItem" onClick={() => {
                        if (needBuy) {
                            stripeStore.amount = item.price;
                            stripeStore.objectid = item.id;
                            stripeStore.ordertype = 3;
                            stripeStore.showStripe = true;
                            return;
                        }
                        creatorVideoStore.selectedItem = item;
                    }}>
                        {creatorVideoStore.isEditing && <div className="tools">
                            <div className="edit" onClick={(e) => {
                                e.stopPropagation()
                                creatorVideoStore.editVideoId = item.id;
                            }}>修改</div>
                            <div className="delete" onClick={(e) => {
                                e.stopPropagation()
                                creatorVideoStore.deleteVideo({ objectid: item.id, objecttype: 2 }, () => {
                                    let list: CreatorVideoItem[] = [];
                                    creatorVideoStore.list.forEach((l) => {
                                        if (l.id != item.id) {
                                            list.push(l)
                                        }
                                    })
                                    creatorVideoStore.list = list;
                                });
                            }}>删除</div>
                        </div>}
                        <div className="thumb">
                            <img src={item.thumb} alt="" />
                        </div>
                        <p className="name">{item.title}.</p>
                        <div className="div-block-41">
                            <div className="view-left">
                                <EyeOutlined style={{ color: '#5F5E61' }} />
                                <div className="viewCount">{0}</div>
                                <StarOutlined style={{ color: '#5F5E61' }} />
                                <HeartOutlined style={{ color: '#5F5E61' }} />
                            </div>
                            <div>
                                <div className="date">{item.createtime.slice(0, 10)}</div>
                            </div>
                        </div>
                        {!creatorVideoStore.isEditing && <div className="mask">
                            {needBuy ? <div className='price'>${item.price}</div> : <img
                                src={Icon_Play}
                                loading="lazy" alt="" className="image-23" />}

                        </div>}
                    </li>
                })
                }
            </ul>
        </div>


        <EditVideoModal visible={creatorVideoStore.editVideoId != undefined} cancle={(data: any) => {
            creatorVideoStore.editVideoId = undefined
            if (data) {
                let list: CreatorVideoItem[] = [];
                creatorVideoStore.list.forEach((l) => {
                    if (l.id == data.id) {
                        if (data.title) {
                            l.title = data.title
                        }
                        if (data.thumb) {
                            l.thumb = data.thumb;
                        }
                    }
                    list.push(l)
                })

                creatorVideoStore.list = list;
            }
        }} />
        <StripeModal visible={stripeStore.showStripe} success={() => {
            stripeStore.showStripe = false;
            let list: CreatorVideoItem[] = [];
            creatorVideoStore.list.forEach((l) => {
                if (l.id == stripeStore.objectid) {
                    l.lookauth = 1;
                }
                list.push(l)
            })
            creatorVideoStore.list = list;
        }} cancle={() => { stripeStore.showStripe = false }} />
        {creatorVideoStore.loadingMore && <div className="loadmore">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
        </div>}

    </div>
})