import { Modal } from 'antd';
import { observer } from 'mobx-react';
import { Form, Input, Button } from 'antd';
import { userStore } from '../../stores/UserStore';
import { settingStore } from '../../stores/SettingStore';
import { ImageUpload } from '../Publish/ImageUpload';
import './CreatorVideos.scss';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';

interface EditModalProps {
    visible: boolean;
    cancle: Function;
}

export const EditCollectionModal = observer((props: EditModalProps) => {
    let cover: string;
    return (
        <Modal title='修改锦集信息' visible={props.visible}
            width={400}
            footer={null}
            centered={true}
            onCancel={() => { props.cancle() }}
        >
            <Form
                name="normal_login"
                className="login-form edit-form"
                initialValues={{ remember: true }}
                onFinish={(v) => {
                    let data: any = {
                        id: creatorVideoStore.editCollectionId
                    }
                    if (v.name) {
                        data.name = v.name;
                    }
                    if (cover) {
                        data.cover = cover;
                    }
                    if (v.price) {
                        data.price = v.price;
                    }
                    if (v.trycount) {
                        data.trycount = v.trycount;
                    }
                    creatorVideoStore.editCollection(data,()=>{
                        props.cancle(data)
                    });
                }}
            >
                <Form.Item
                    name="name"
                    label="锦集名称"
                // initialValue={userStore.userInfo?.username}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="price"
                    label="锦集价格"
                // initialValue={userStore.userInfo?.username}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="trycount"
                    label="试看集数"
                // initialValue={userStore.userInfo?.username}
                >
                    <Input />
                </Form.Item>

                <div className="cover-form">
                    <div className="title">
                    锦集封面：
                    </div>
                    <div className="cover">
                        <ImageUpload callback={(url: string) => {
                            cover = url;
                        }} />
                    </div>
                </div>

                <Form.Item>
                    <Button htmlType="submit" className="login-form-button" >
                        提交
                    </Button>
                </Form.Item>
            </Form>
        </Modal>
    );
})













