
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Avatar, Spin } from 'antd';
import { LoadingOutlined} from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import './CreatorVideos.scss';
import NoData from '../Stock/NoData';
import { useHistory } from 'react-router';

export const SubscribeListPage = observer(() => {
    useEffect(() => {
        creatorVideoStore.getSubscribeList();
    }, []);
  let history = useHistory();
  return <div className="videoWrapper">

        <ul className="userContent hotCreater followList" >
            {creatorVideoStore.loading ? <div className="loading">
                <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
            </div> : creatorVideoStore.followList.length == 0 ? <NoData /> : creatorVideoStore.followList.map((user, idx) => {

                return <div className='item user' key={idx} onClick={() => {
                    window.scrollTo(0, 0);
                    history.push(`/creatorVideos/${user.id}`);
                    creatorVideoStore.userId = user.id;
                    creatorVideoStore.getUser();

                }}>
                    <div className="top-profile">
                        <Avatar src={user?.avatar} />
                        <div className="info">
                            <div className="name">{user?.username}</div>
                            <div className="amount">{user?.followcount}订阅</div>
                        </div>
                    </div>
                </div>

            })
            }
        </ul>
        {creatorVideoStore.loadingMore && <div className="loadmore">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
        </div>}
    </div>
})