import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Avatar, Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import eventBus from '../../utils/eventBus';
import { Sub_Bg_Img } from '../../images/SubBg';
import { creatorCenterStore } from '../../stores/CreatorCenterStore';
import { Input } from 'antd';
import NoData from '../Stock/NoData';
import { useHistory } from 'react-router';
import '../Videos/Videos.scss';
import './CreatorCenter.scss'

const { Search } = Input;

export const CreatorUserList = observer((props: any) => {
    useEffect(() => {
        creatorCenterStore.getAllUsers();
    }, []);
    eventBus.addListener('toBottom', () => {
        creatorCenterStore.getAllUsers(true);
    })
   
  let history = useHistory();
  return (
        <div className="videoPage">
            <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
                <div className="headContent">
                    <div className="title">傲利创作者</div>
                    <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div>
                </div>
            </header>
            <div className="videoWrapper">
                <div className="topNav">
                   
                    <div className="search">
                        <Search placeholder="请输入关键词" onChange={(v) => {
                            // researchStore.keyword = v.target.value;
                        }} onSearch={(v) => {
                            var researchContent: any = document.getElementsByClassName('researchContent')[0];
                            var _top: number = researchContent.offsetTop;
                            window.scrollTo(0, _top - 150);
                            // researchStore.getData();
                        }} enterButton />
                    </div>
                </div>
                <ul className="userContent hotCreater" >
                    {creatorCenterStore.loading ? <div className="loading">
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div>   :  creatorCenterStore.users.length==0? <NoData />: creatorCenterStore.users.map((user, idx) => {
                       
                        return <div className='item user' key={idx} onClick={()=>{
                            window.scrollTo(0,0);
                            history.push(`/creatorVideos/${user.id}`)
            
                          }}>
                        <div className="top-profile">
                          <Avatar src={user?.avatar} />
                          <div className="info">
                            <div className="name">{user?.username}</div>
                            <div className="amount">{user?.followcount}订阅</div>
                          </div>
                        </div>
                      </div>
                      
                    })
                    }
                </ul>
                {creatorCenterStore.loadingMore && <div className="loadmore">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
                </div>}
            </div>
        </div >
    )
});