
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import { Icon_Play } from '../../images/icons/Play';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import './CreatorVideos.scss';
import {
  GridContextProvider,
  GridDropZone,
  GridItem,
  swap
} from "react-grid-drag";
import Icons from '../../images/images';
import { Top_Bg_Img } from '../../images/topBg';
import AwesomeSlider from 'react-awesome-slider';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { showError } from '../../components/Message';
import { EditVideoModal } from './EditVideo';
import { StripeModal } from '../Stripe/Stripe';
import { stripeStore } from '../../stores/StripeStore';
import { CreatorVideoItem } from '../../services/CreatorVideoService';
import { CommentPage } from '../Creator/Comment';


export const CollectionDetailPage = observer((props: any) => {
  useEffect(() => {
    let id = props.match.params.id;
    creatorVideoStore.getCollectionDetail(id);
  }, []);
  let price: number = parseFloat(creatorVideoStore.collectionItem?.price || '0');
  return <div className="creatorVideoPage">
    <img className="headerTop" src={Top_Bg_Img} alt="" />
    <ul className="content collectionDetailContent">
      <div className="tabs">
        <img className="back" src={Icons.back} alt="" onClick={() => {
          creatorVideoStore.collectionItem = undefined
          props.history.goBack()
        }} />

        <div className="title">{creatorVideoStore.collectionItem?.name}</div>
        {price > 0 ? <div className="pay" onClick={() => {
          stripeStore.pay(price.toString());
        }}>购买：${price}</div> : <div></div>}
      </div>
      <div className="describe">简介：{creatorVideoStore.collectionItem?.describe || '暂无'}</div>
      {creatorVideoStore.isSelf && <div className="editWrapper">
        <div className="edit" onClick={() => { creatorVideoStore.isEditing = !creatorVideoStore.isEditing }}>编辑</div>
        <div className="edit" onClick={() => { creatorVideoStore.isSorting = !creatorVideoStore.isSorting }}>排序</div>
      </div>}
      {creatorVideoStore.isSorting && <div className="sortInfo">
        <div className="info">鼠标可拖动视频进行排序</div>
        <div className="btns">
          <div className="cancel" onClick={() => { creatorVideoStore.isSorting = false; creatorVideoStore.cancelOrder() }}>取消</div>
          <div className="confirm" onClick={() => {
            let id = props.match.params.id;
            creatorVideoStore.sortVideo(id);
          }}>确定</div>
        </div>

      </div>}
      {creatorVideoStore.loadingDetail ? <div className="loading">
        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
      </div> :

        <GridContextProvider onChange={creatorVideoStore.sortCollectionOrder.bind(creatorVideoStore)}>
          <GridDropZone
            id="items"
            boxesPerRow={4}
            rowHeight={270}
            disableDrag={!creatorVideoStore.isSorting}
            style={{ width: "100%", height: "800px" }}
          >
            {creatorVideoStore.detailList.map((item, idx) => {
              let needBuy = item.lookauth != 1
              console.log("needBuyneedBuyneedBuy ${}", needBuy);
              return (
                <GridItem key={item.id} style={{ zIndex: "9999" }}>
                  <li key={idx} role="listitem" className="videoItem" onClick={() => {

                    if (creatorVideoStore.isEditing || creatorVideoStore.isSorting) {
                      return;
                    }
                    if (needBuy) {
                      showError("购买合集后可观看剩余集数")
                      return;
                    }
                    if (!item.url) {
                      showError("视频播放地址异常！");
                      return;
                    }
                    creatorVideoStore.selectedVideoItem = item
                  }}>
                    {creatorVideoStore.isEditing && <div className="tools">
                      <div className="edit" onClick={(e) => {
                        e.stopPropagation()
                        creatorVideoStore.editVideoId = item.id;
                      }}>修改</div>
                      <div className="delete">删除</div>
                    </div>}
                    <div className="thumb">
                      <img src={item.thumb} alt="" />
                    </div>
                    <p className="name">{item.title}.</p>
                    <div className="div-block-41">
                      <div className="view-left">
                        <EyeOutlined style={{ color: '#5F5E61' }} />
                        <div className="viewCount">{0}</div>
                        <StarOutlined style={{ color: '#5F5E61' }} />
                        <HeartOutlined style={{ color: '#5F5E61' }} />
                      </div>
                      <div>
                        <div className="date">{item.createtime.slice(0, 10)}</div>
                      </div>

                    </div>
                    {!creatorVideoStore.isSorting && !creatorVideoStore.isEditing && <div className="mask">
                      {needBuy ? <div className='price'>${price}</div> : <img
                        src={Icon_Play}
                        loading="lazy" alt="" className="image-23" />}

                    </div>}
                  </li>
                </GridItem>
              )
            })}
          </GridDropZone>
        </GridContextProvider>
      }
    </ul>

    {
      creatorVideoStore.selectedVideoItem &&
      <div className="videoContent">
        <div className="tabs">
          <img className="back" src={Icons.close} alt="" onClick={() => {
            creatorVideoStore.selectedVideoItem = undefined;
          }} />

          {/* <div className="items">
                            <div className="like">
                                <img src={true ? Icons.icon_like_s : Icons.icon_like} alt="" onClick={() => {
                                    // researchDetailStore.like();
                                }} />
                                <div className="count">{22}</div>
                            </div>
                            <img className='follow' src={true ? Icons.follow_s : Icons.follow} alt="" onClick={() => {
                                // researchDetailStore.follow();
                            }} />
                        </div> */}
        </div>
        <div className="content">
          <div className='videoPlayer'>
            <AwesomeSlider
              onFirstMount={(e: any) => {
                var video = e.currentSlide.querySelector("video");
                video.autoplay = true;
                video.controlsList = "nodownload";
                video.disablePictureInPicture = "true";
                video.οncοntextmenu = "return false";
              }}
              onTransitionStart={(e: any) => {
                var video = e.currentSlide.querySelector("video");
                video.controlsList = "nodownload";
                video.disablePictureInPicture = "true";
                video.οncοntextmenu = "return false";
                if (video !== null && !video.paused) {
                  video.pause();
                }
              }}
              onTransitionEnd={(e: any) => {
                var video = e.currentSlide.querySelector("video");
                video.controlsList = "nodownload";
                video.οncοntextmenu = "return false";
                video.οncοntextmenu = "return false";
                video.play();
              }}
              bullets={false}
              selected={creatorVideoStore.detailList.filter((l) => l.lookauth ==1).map((l) => l.id).indexOf(creatorVideoStore.selectedVideoItem?.id || '')}
              media={creatorVideoStore.detailList.filter((l) => l.lookauth ==1).map((item) => { return { source: item.url } })}
            />
            <div className="title">
              {creatorVideoStore.selectedVideoItem?.title}
            </div>
            <CommentPage objectid={creatorVideoStore.selectedVideoItem.id} objecttype={5}/>
          </div>
          <div className="info">
            <div className="videoList">
              {creatorVideoStore.detailList.filter((l) => l.lookauth ==1).map((item, idx) => {
                if (!item) {
                  return <li className="item"></li>
                }
                return <li className="item" key={idx} onClick={() => {
                  let needBuy = item.lookauth != 1
                  if (needBuy) {
                    stripeStore.amount = item.price;
                    stripeStore.objectid = item.id;
                    stripeStore.ordertype = 4;
                    stripeStore.showStripe = true;
                    return;
                  }
                  creatorVideoStore.selectedVideoItem = item;
                }}>

                  <LazyLoadImage
                    alt={item.title}
                    effect="opacity"
                    src={item.thumb} />
                  <div className="title">{item.title}</div>
                </li>
              })}
            </div>
          </div>

        </div>


      </div>
    }

    <EditVideoModal visible={creatorVideoStore.editVideoId != undefined} cancle={() => { creatorVideoStore.editVideoId = undefined }} />
    <StripeModal visible={stripeStore.showStripe} success={() => {
            stripeStore.showStripe = false;
            let list: CreatorVideoItem[] = [];
            creatorVideoStore.detailList.forEach((l) => {
                    l.lookauth = 1;
                list.push(l)
            })
            creatorVideoStore.detailList = list;
        }} cancle={() => { stripeStore.showStripe = false }} />
    {creatorVideoStore.loadingMore && <div className="loadmore">
      <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
    </div>}
  </div>
})