import { makeAutoObservable, } from "mobx";
import { showError, showLoading, showSuccess } from "../components/Message";
import { CollectionItem, CollectionListService } from "../services/CollectionListService";
import { CreatorNewsItem, CreatorUser, CreatorUserListResponse, CreatorUserListService } from "../services/CreatorService";
import { CreatorVideoItem, CreatorVideoService } from "../services/CreatorVideoService";

class CreatorCenterStore {
  public loading: boolean = false;
  public loadingMore: boolean = false;
  public loadingHotUser: boolean = false;
  public loadingHotVideo: boolean = false;
  public loadingHotCollection: boolean = false;
  public userList: CreatorUser[] = [];
  public hotVideos:CreatorVideoItem[] = [];
  public videos:CreatorVideoItem[] = [];
  public collections: CollectionItem[] = [];
  public users: CreatorUser[] = [];
  public hotCollections: CollectionItem[] = [];
  public detail?: CreatorNewsItem;
  private _page: number = 1;
  public noMore: boolean = false;
  public type:string = 'free';
  public selectedItem?:CreatorVideoItem;
  public isfree:boolean = true;
  public searchkey:string='';

  constructor() {
    makeAutoObservable(this);
  }

  public async getHotVideos() {
    if (this.loadingHotVideo) {
      return;
    }
    let response: CreatorVideoItem[];
    this.loadingHotVideo = true
    try {
      response = await new CreatorVideoService({ishot:1,psize:6}).send();
    } catch (e: any) {
      this.loadingHotVideo = false;
      return;
    }
    this.loadingHotVideo = false;
    this.hotVideos = response;
  }

  public async getHotCollections() {

    if (this.loadingHotCollection) {
      return;
    }
    let response: CollectionItem[];
    this.loadingHotCollection = true
    try {
      response = await new CollectionListService({ishot:1,psize:6}).send();
    } catch (e: any) {
      this.loadingHotCollection = false;
      return;
    }
    this.loadingHotCollection = false;
    this.hotCollections = response;
  }


  public async getHotUsers() {

    if (this.loadingHotUser) {
      return;
    }
    let response: CreatorUserListResponse;

    this.loadingHotUser = true

    try {
      response = await new CreatorUserListService({ishot:1,psize:6}).send();
    } catch (e: any) {
      this.loadingHotUser = false;
      return;
    }
    this.loadingHotUser = false;
    this.userList = response.list;
  }


  public async getAllVideos(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CreatorVideoItem[];

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
        response = await new CreatorVideoService({p:this._page,psize:20,isfree:this.isfree?0:1,searchkey:this.searchkey}).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.length ==0;
      this.videos.push(...response);
    } else {
      this.videos = response;
    }
  }

  

  public async getAllCollection(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CollectionItem[];

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
        response = await new CollectionListService({p:this._page,psize:20,isfree:this.isfree?0:1,searchkey:this.searchkey}).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;

    if (loadmore) {
      this.noMore = response.length ==0;
      this.collections.push(...response);
    } else {
      this.collections = response;
    }
  }

  public async getAllUsers(loadmore: boolean = false) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: CreatorUserListResponse;

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
        response = await new CreatorUserListService({p:this._page,psize:20}).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;
    if (loadmore) {
      this.noMore = response.list.length ==0;
      this.users.push(...response.list);
    } else {
      this.users = response.list;
    }
  }

}

const creatorCenterStore = new CreatorCenterStore();

export { creatorCenterStore };