import { useEffect, useState } from 'react';
import { showError, showLoading, showSuccess } from '../../components/Message';
import { CommentDetailService, CommentItem, CommentListService, CommentReplyService } from '../../services/CommentService';
import { userStore } from '../../stores/UserStore';
import './Comment.scss';
import { CommentSubItemPage } from './CommentSubItem';

export interface CommentItemProps {
    item: CommentItem,
    success: Function
}

export const CommentItemPage = (props: CommentItemProps) => {
    const [fold, setFold] = useState(false);
    const [reply, setReply] = useState(false);
    const [loading, seLoading] = useState(false);
    const [replyList, setReplyList] = useState([] as CommentItem[]);
    const [ownReplyList, setOwnReplyList] = useState([] as CommentItem[]);

     useEffect(()=>{ 
        setOwnReplyList([...(props.item.list??[])])
     },[])

    var getReplyList = async () => {
        if (loading) {
            return;
        }
        seLoading(true);
        let response: CommentItem[];
        try {
            response = await new CommentDetailService(props.item?.id || '').send();
        } catch (e) {
            showError(`${e}`);
            seLoading(false);
            return;
        }
        var list:any =[];
        response.forEach((r:any)=>{
            var _list = ownReplyList.map((o) => o.id);
            if(_list.indexOf(r.id) == -1){
                list.push(r)
            }
        })
        seLoading(false);
        setReplyList(list.reverse());
    }


    let replyText = '';

    var replyComment = async () => {
        if (!replyText) {
            showError('发言不可为空！');
            return;
        }
        showLoading("发布中...")
        try {
            await new CommentReplyService(parseInt(props.item?.objectid || ''), props.item?.objecttype || '1', replyText, props.item.id || '', props.item.id || '').send();
        } catch (e) {
            showError(`${e}`);
            return;
        }
        showSuccess("发布成功！");

        var Dates = new Date();
        var Y = Dates.getFullYear();
        var M = Dates.getMonth() + 1;
        var D = Dates.getDate();
        var H = Dates.getHours();
        var Mu = Dates.getMinutes();
        var times = Y + (M < 10 ? "-0" : "-") + M + (D < 10 ? "-0" : "-") + D + ' ' + (H < 10 ? "0" : "") + H + (Mu < 10 ? ":0" : ":") + Mu;

        let _list = JSON.parse(JSON.stringify(ownReplyList));
        let _resultList = [{
            username: userStore.userInfo?.username,
            userimg: userStore.userInfo?.avatar,
            content: replyText,
            createtime: times
        }]
        _resultList.push(..._list);
        setOwnReplyList(_resultList);
        replyText = '';
        props.item.commentcount = `${(parseInt( props.item.commentcount ||'') + 1)}`;
        setReply(false)
    }

     var _replyCount = parseInt(props.item.commentcount ||'') - ownReplyList.length;
     var replyCount = _replyCount?`${_replyCount}`:'';
    return (  

        <div className="ttp-comment-item">
            <a aria-hidden="true" href="/c/user/4177015844/?source=tuwen_detail" target="_blank" className="">
                <div className="ttp-avatar auth-0 auth">
                    <img src={props.item.userimg} alt="" />
                </div>
            </a>
            <div className="comment-info">
                <div className="header">
                    <div className="user-info">
                        <a href="/c/user/4177015844/?source=tuwen_detail" target="_blank">
                            <div className="user-name">
                                <span className="name">{props.item.username}</span>
                            </div>
                        </a>
                    </div>
                    {/* <div className="right-action">
                                            <div role="button" aria-label="点赞997" aria-pressed="false" className="ttp-comment-like">
                                                <div className="inner">
                                                    <span className="">997</span>
                                                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M13.6547 9.01851L13.0678 8.94922C13.048 9.11689 13.1008 9.28501 13.213 9.41119C13.3251 9.53738 13.4859 9.60957 13.6547 9.60957V9.01851ZM14.0618 5.57064L14.6487 5.63993L14.0618 5.57064ZM9.04306 8.57151L8.52223 8.29208L9.04306 8.57151ZM5.9138 18.7304L5.64546 19.2571L5.9138 18.7304ZM5.03097 17.8476L5.55761 17.5793L5.03097 17.8476ZM18.4092 18.0512L18.901 18.3791L18.4092 18.0512ZM17.5543 18.7741L17.796 19.3135L17.5543 18.7741ZM19.6188 11.9347L19.036 11.8365L19.6188 11.9347ZM19.7277 10.0391L19.1733 10.2442L19.7277 10.0391ZM19.0402 9.22612L18.7459 9.73867L19.0402 9.22612ZM4.81177 10.9756L4.221 10.9572L4.81177 10.9756ZM6.7679 9.0195L6.74946 8.42872L6.7679 9.0195ZM19.036 11.8365L18.308 16.1572L19.4737 16.3536L20.2017 12.0329L19.036 11.8365ZM15.7035 18.3596H8.043V19.5417H15.7035V18.3596ZM5.40185 15.7184V11.1189H4.21973V15.7184H5.40185ZM13.6547 9.60957H17.1529V8.42745H13.6547V9.60957ZM9.5639 8.85094L11.582 5.08933L10.5403 4.53047L8.52223 8.29208L9.5639 8.85094ZM13.4748 5.50135L13.0678 8.94922L14.2417 9.0878L14.6487 5.63993L13.4748 5.50135ZM6.91118 9.60957H8.29598V8.42745H6.91118V9.60957ZM13.0901 4.69329C13.3631 4.86625 13.5126 5.18044 13.4748 5.50135L14.6487 5.63993C14.7399 4.86742 14.3799 4.11109 13.7228 3.69475L13.0901 4.69329ZM8.52223 8.29208C8.47751 8.37544 8.39058 8.42745 8.29598 8.42745V9.60957C8.82611 9.60957 9.31329 9.31808 9.5639 8.85094L8.52223 8.29208ZM8.043 18.3596C7.46756 18.3596 7.07152 18.3591 6.76431 18.334C6.464 18.3095 6.30099 18.2644 6.18214 18.2038L5.64546 19.2571C5.95874 19.4167 6.29464 19.4817 6.66805 19.5122C7.03456 19.5421 7.48706 19.5417 8.043 19.5417V18.3596ZM4.21973 15.7184C4.21973 16.2743 4.21927 16.7268 4.24921 17.0934C4.27972 17.4668 4.34471 17.8027 4.50433 18.1159L5.55761 17.5793C5.49705 17.4604 5.45195 17.2974 5.42741 16.9971C5.40231 16.6899 5.40185 16.2939 5.40185 15.7184H4.21973ZM6.18214 18.2038C5.91324 18.0668 5.69462 17.8482 5.55761 17.5793L4.50433 18.1159C4.75468 18.6073 5.15414 19.0067 5.64546 19.2571L6.18214 18.2038ZM18.308 16.1572C18.2258 16.6448 18.1692 16.9782 18.1056 17.2359C18.0437 17.4863 17.9844 17.6228 17.9175 17.7233L18.901 18.3791C19.0736 18.1203 19.1745 17.8378 19.2531 17.5196C19.33 17.2085 19.3945 16.8237 19.4737 16.3536L18.308 16.1572ZM15.7035 19.5417C16.1802 19.5417 16.5704 19.5421 16.8899 19.5179C17.2167 19.4932 17.5121 19.4407 17.796 19.3135L17.3127 18.2347C17.2025 18.284 17.058 18.3197 16.8008 18.3392C16.5362 18.3592 16.198 18.3596 15.7035 18.3596V19.5417ZM17.9175 17.7233C17.7681 17.9473 17.5584 18.1246 17.3127 18.2347L17.796 19.3135C18.245 19.1124 18.628 18.7884 18.901 18.3791L17.9175 17.7233ZM20.2017 12.0329C20.2844 11.5417 20.3533 11.136 20.38 10.8048C20.4072 10.4682 20.3975 10.146 20.282 9.83395L19.1733 10.2442C19.2025 10.3231 19.2229 10.4471 19.2017 10.7098C19.1801 10.978 19.1222 11.3245 19.036 11.8365L20.2017 12.0329ZM17.1529 9.60957C17.6721 9.60957 18.0234 9.61009 18.2915 9.63332C18.554 9.65607 18.6729 9.69676 18.7459 9.73867L19.3346 8.71357C19.0461 8.54788 18.73 8.48476 18.3935 8.45561C18.0625 8.42693 17.651 8.42745 17.1529 8.42745V9.60957ZM20.282 9.83395C20.1071 9.36113 19.7718 8.96465 19.3346 8.71357L18.7459 9.73867C18.9431 9.85196 19.0944 10.0308 19.1733 10.2442L20.282 9.83395ZM11.582 5.08933C11.8743 4.54443 12.5678 4.36233 13.0901 4.69329L13.7228 3.69475C12.6206 2.99633 11.1572 3.3806 10.5403 4.53047L11.582 5.08933ZM5.40185 11.1189C5.40185 11.0399 5.40193 11.014 5.40255 10.9941L4.221 10.9572C4.21966 11.0002 4.21973 11.0488 4.21973 11.1189H5.40185ZM6.91118 8.42745C6.84112 8.42745 6.7925 8.42738 6.74946 8.42872L6.78634 9.61027C6.8063 9.60965 6.8322 9.60957 6.91118 9.60957V8.42745ZM5.40255 10.9941C5.42609 10.2398 6.03206 9.63381 6.78634 9.61027L6.74946 8.42872C5.37124 8.47174 4.26402 9.57896 4.221 10.9572L5.40255 10.9941Z" fill="#222222">
                                                        </path>
                                                        <path d="M8.37335 9.26562V19.518" stroke="#222222" stroke-width="1.29874">
                                                        </path>
                                                    </svg>
                                                </div>
                                            </div>
                                        </div> */}
                </div>
                <div className="body">
                    <p className="content">{props.item.content}</p>
                </div>
                <div className="footer">
                    <div className="left-action">
                        {userStore.userInfo?.username != props.item.username && <button className="reply-btn" onClick={() => {
                            console.log("???!!");
                            setReply(!reply);
                        }}>回复</button>}
                        <span className="dot">·</span>
                        <span className="time">{props.item?.createtime?.substring(5, 16)}</span>
                    </div>

                </div>
                {reply && <div className="ttp-comment-input reply-wrapper">
                    <textarea className="comment-textarea" onChange={(v) => {
                        replyText = v.target.value;
                    }} placeholder="说点什么吧...">
                    </textarea>
                    <div className="action">
                        <button type="button" onClick={() => {
                            replyComment();
                        }} className="submit-btn disable">评论</button>
                    </div>
                </div>}

              <div className="reply-list">
                    {ownReplyList.map((l) => {
                        if (l.id == props.item.id) {
                            return <div></div>
                        }
                        return <CommentSubItemPage item={l} success={() => {

                        }} />
                    })
                    }
                </div>
                {parseInt(props.item.commentcount || '') > 0 && replyCount &&!fold && <button className="check-more-reply" onClick={() => {
                    setFold(!fold);
                    if (!replyList.length) {
                        getReplyList();
                    }
                }}>查看全部 {replyCount} 条回复</button>}

                {fold && <div className="reply-list">
                    {replyList.map((l,i) => {
                        if (l.id == props.item.id) {
                            return <div></div>
                        }
                        return <CommentSubItemPage item={l} success={(item: any) => {
                            let _list = JSON.parse(JSON.stringify(replyList));
                            _list.splice(i+1,0,item);
                            setReplyList(_list);
                        }} />
                    })
                    }
                    {fold && <button onClick={() => {
                        setFold(false);
                    }} className="check-more-reply">收起</button>}
                </div>}

            </div>
        </div>


    );
}