
import { AboutUs } from "../pages/AboutUs/AboutUs";
import { CreatorPage } from "../pages/Creator/Creator";
import { CreatorDetail } from "../pages/Creator/CreatorDetail";
import { CreatorCenterPage } from "../pages/CreatorCenter/CreatorCenter";
import { CreatorCollectionList } from "../pages/CreatorCenter/CreatorCollectionList";
import { CreatorUserList } from "../pages/CreatorCenter/CreatorUserList";
import { CreatorVideoList } from "../pages/CreatorCenter/CreatorVideoList";
import Index from "../pages/Index";
import { Kline } from "../pages/Kline/Kline";
import { KlineDetail } from "../pages/Kline/KlineDetail";
import { Main } from "../pages/Main/Main";
import { MarketCalendar } from "../pages/MarketCalendar/MarketCalendar";
import { NewsPage } from "../pages/News/News";
import { PublishNewsPage } from "../pages/Publish/PublishNews";
import { PublishVideoPage } from "../pages/Publish/PublishVideo";
import { Research } from "../pages/Research/Research";
import { ResearchDetail } from "../pages/Research/ResearchDetail";
import { StockIndex } from "../pages/Stock/StockIndex";
import { StockSearch } from "../pages/Stock/StockSearch";
import { WatchListPage } from "../pages/Stock/WatchList/WatchList";
import { Subscribe } from "../pages/Subscribe/Subscribe";
import { Videos } from "../pages/Videos/Videos";
import { CollectionDetailPage } from "../pages/VideosCreator/CollectionDetaill";
import { CreatorVideos } from "../pages/VideosCreator/Videos";





export const routesConfig = [
    {
        path: "/",
        component: Index,
        routes: [
            {
                path: "/",
                exact: true,
                component: Main
            },
            {
                path: "/news",
                component: NewsPage
            },
            {
                path: "/aboutUs",
                component: AboutUs
            },
            {
                path: "/research",
                component: Research,
                rule: 'researchReport'
            }, {
                path: "/researchDetail/:id",
                component: ResearchDetail,
                rule: 'researchReport'
            }
            , {
                path: "/subscribe",
                component: Subscribe,
            },

            {
                path: "/videos",
                component: Videos,
                // register:true
            },
            {
                path: "/watchList",
                component: WatchListPage,
                // register:true
            },
            {
                path: "/stockSearch",
                component: StockSearch,
            }
            ,
            {
                path: "/stockDetail/:symbol/:strategy",
                component: StockIndex,
            },
            {
                path: "/publishVideo",
                component: PublishVideoPage,
            },
            {
                path: "/publishNews",
                component: PublishNewsPage,
            },
            {
                path: "/creatorCenter",
                component: CreatorCenterPage,
            },
            {
                path: "/creator",
                component: CreatorPage,
            },
            {
                path: "/creatorVideoList",
                component: CreatorVideoList,
            },
            {
                path: "/creatorCollectionList",
                component: CreatorCollectionList,
            },
            {
                path: "/creatorUserList",
                component: CreatorUserList,
            },
            
            {
                path: "/creatorNewsDetail",
                component: CreatorDetail,
            },
            {
                path: "/creatorVideos/:id",
                component: CreatorVideos,
            },
            {
                path: "/creatorDetail/:id",
                component: CollectionDetailPage,
            },
            
            {
                path: "/calendar",
                component: MarketCalendar,
            },
            {
                path: "/kline",
                component: Kline,
            },

            {
                path: "/klineDetail/:strategy",
                component: KlineDetail,
            }
        ]
    },
];



