import './Kline.scss';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Loading } from '../../components/Loading';
import Icons from '../../images/images';
import eventBus from '../../utils/eventBus';
import { Top_Bg_Img } from '../../images/topBg';
import { klineStore } from '../../stores/KlineStore';
import { StrategiesMap } from '../../services/StatisticService';
import { SymbolItem } from '../../services/KlineDetailService';


export const KlineDetail = observer((props: any) => {
  let strategy: string = props.match.params.strategy;
  useEffect(() => {
    klineStore.getDetailData(strategy);
  }, []);
  var strategyMap = StrategiesMap[klineStore.detail?.pattern_name.en || ''] || {};
  return (
    <div className="klineDetailWrapper">
      <img className="headerTop" src={Top_Bg_Img} alt="" />
      <div className="klineDetailContent" >
        <div className="tabs">
          <img className="back" src={Icons.back} alt="" onClick={() => {
            eventBus.emit('changeWhiteHeaderBg', false);
            props.history.goBack()
          }} />

          <div className="title">{klineStore.detail?.pattern_name.ch}</div>

        </div>
        {klineStore.detailloading ? <Loading /> : <div className="content">
          <div className="klineItem ">
            <div className="tit-pic ">
              {strategy && <img src={strategyMap['icon']} />}
            </div>
            <div className="content" >
              <div className="info"> {klineStore.detail?.description.ch}</div>
              {/* <div className="date"> 更新时间{'2021-01-02'}</div> */}
            </div>
          </div>

          <div className="list">
            {klineStore.detail?.symbols.map((s: SymbolItem, i) => {
              return <div key={i} className="item" onClick={()=>{
                window.scrollTo(0, 0);
                props.history.push(`/stockDetail/${s.symbol}/${klineStore.detail?.pattern_name.en}`);
              }}>
                <div className="symbol">{s.symbol[0]}</div>
                <div className="info">
                  <div className="title">{s.symbol}</div>
                  <div>{s.name}</div>
                </div>
                <div className="price">{s.latest_price}</div>
                <div className="right">
                  <div className="value" style={{color:s.latest_return>0?'#52CFBF':'#FF505F'}}>{(s.latest_return * 100).toFixed(2)}%</div>
                  <div className="value" style={{color:s.latest_change>0?'#52CFBF':'#FF505F'}}>{s.latest_change}</div>
                </div>
              </div>
            })}
          </div>
        </div>}

      </div>
    </div>
  )
});