import { Icon_Index_Stock } from '../../images/icons/Price';
import { observer } from 'mobx-react';
import './StockIndex.scss';
import { Search_Bg_Img } from '../../images/SearchBg';
import { StockSearchInput } from './Search/SearchInput';

export const StockSearch = observer(() => {

    return (
        <div className="stock-search" >
            <div className="section-5 wf-section" style={{ backgroundImage: `url('${Search_Bg_Img}')`, backgroundSize: '100% 100%' }}>
                <div className="container-2 w-container">
                    <div className="columns w-row" style={{ width: '100%' }}>
                        <div className="column-4 ">
                            <h1 className="heading-8">
                                Analyse
                                <span data-w-id="2df84f67-c9c0-c8ec-eb23-5d7357533117"
                                >
                                    &nbsp;smart
                                </span>
                                <br />
                                Dream <span className="text-span-3">Big</span><span data-w-id="2df84f67-c9c0-c8ec-eb23-5d735753311c" className="text-span-4"
                                    style={{ color: 'rgb(129, 164, 240)' }}>.</span></h1>
                           <StockSearchInput blue={false}/>
                            <div className="div-block-31"><img
                                src={Icon_Index_Stock}
                                loading="lazy" width="56" alt="" />
                                <div className="div-block-32"><a href="/labs" className="link-block-5 w-inline-block">
                                    <h4 className="heading-9">oli 行情</h4>
                                </a>
                                    <p className="paragraph-4">多元化的视野，专业的知识储备，对市场交易进行深度分析，掌握市场风向，将金融技术和金融服务结合.
                                      </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

        </div>
    );
})


