import { Ajax } from '../utils/http';

export class StockCalendarService {
    private start: string;
    private end: string;
    constructor(_start: string,_end:string) {
        this.start = _start;
        this.end = _end;
    }

    public async send(): Promise<CalendarItem[]> {
        var response: CalendarItem[];
        var data = {
            start: this.start,
            end: this.end,
        }
        try {
            response = await new Ajax<CalendarItem[]>( '/economic_calendar', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

export interface CalendarItem {
    actual: number;
    country: string;
    estimate: number;
    event: string;
    impact: string;
    prev: number;
    time: string;
    unit: string;
  }
  
  