import { observer } from 'mobx-react';
import ChartStore from '../../../stores/ChartStore';
import StatisticStore from '../../../stores/StatisticStore';
import { useEffect } from 'react';
import './Search.scss';
import { StockSearchInput } from './SearchInput';
import Icons from '../../../images/images';
import { StockFollowItem } from '../../../services/StockFollowService';
import NoData from '../NoData';
import { Icon_Fly } from '../../../images/icons/Fly';
import { verifyRule } from '../../../utils/globalFuncs';
import { LocalStorageService } from '../../../utils/storage';
import { userStore } from '../../../stores/UserStore';
import { Loading } from '../../../components/Loading';
import { StockLogo } from '../StockLogo';
export const Search = observer(() => {
       try{
    var symbolList: Array<string> = StatisticStore.summary?.symbol.split('_') || [''];
    var yearsList: Array<string> = StatisticStore.incomes?.financial_years || ['', ''];
    var revenueList: Array<number> = StatisticStore.incomes?.total_revenues || [];
    var incomeList: Array<number> = StatisticStore.incomes?.net_incomes || [];
    var assetList: Array<number> = StatisticStore.assets?.total_assets || [];
    var liabsList: Array<number> = StatisticStore.assets?.total_liabs || [];
    var year: string = yearsList[Math.min(yearsList.length-1,1)].split("-").reverse().join('/');
    var income: number = (((revenueList[0] - revenueList[1]) / revenueList[1]) * 100);
    var netIncome: number = (((incomeList[0] - incomeList[1]) / incomeList[1]) * 100);
    var curAsset = assetList[0] - liabsList[0];
    var lastAsset = assetList[1] - liabsList[1];

    var netAssets: number = (((curAsset - lastAsset) / lastAsset) * 100);
    var curClose = ChartStore.stockData[0]?.close;
    var preClose = ChartStore.stockData[1]?.close;
    var incomeTip: string = income === Infinity ? '迅速' : `了${Math.abs(income).toFixed(2)}%`;

    var symbol: string = StatisticStore.summary?.symbol || '';
    if(!symbol){
     return <div></div>
    }
    return (
        <div className="topWrapper" style={{ height: ChartStore.contentHeight }}>
            <StockSearchInput
                blue={true}
                // TODO: 图标等待替换
                searchBtn={<a className="searchBtn">
                    <img src={Icon_Fly} />
                </a>}
            />
            <div className="info">
                <div className="stock-info">
                    <StockLogo symbol={StatisticStore.summary?.symbol} />
                    <div className='top-info'>
                        <div className="title-info">
                            <div className="title">{symbolList[0]}_{symbolList[1]}</div>
                        </div>
                        <div className="abstract">{StatisticStore.summary?.name} </div>

                    </div>
                </div>
                <div className='bottom-info'>
                    {ChartStore.stockData && <div className="data">
                        {curClose}&nbsp;&nbsp;
                        <span>
                        ({((curClose - preClose) / preClose * 100).toFixed(2)}%)
                        </span>
                    </div>}
                    {StatisticStore.followedSymbols.indexOf(symbol.toLowerCase()) == -1 && <img className="follow" src={Icons.icon_add} onClick={async () => {
                        let isLogin = LocalStorageService.get("token");
                        if (isLogin) {
                            StatisticStore.follow();
                        } else {
                            userStore.loginModalVisible = true;
                        }
                    }}></img>}
                </div>
            </div>

            <div className="introduce">
                {symbolList[0]}公司从{year}结束的财政年度开始：收入{income>0?'上升':'下降'}{incomeTip}；净收入{netIncome>0?'上升':'下降'}了{ Math.abs(netIncome).toFixed(2)}%；净资产{netAssets>0?'上升':'下降'}了{Math.abs(netAssets).toFixed(2)}%。
            </div>

            <div className="followWrapper" >
                <div className="followTitle">关注列表</div>
                {StatisticStore.getfollowLoading?<Loading />: StatisticStore.followedItems.length == 0 ?
                    <div style={{ height: '80px', margin: "0 auto" }}>
                        {/*
                            TODO: 样式问题
                            1. 暂无数据, 图片需要更换
                            2. 样式太通用不敢改, 但应该需要调整下
                        */}
                        <NoData />
                    </div>
                    :
                    <div className="list follow-list">
                        {StatisticStore.followedItems.map((f: StockFollowItem, i) => {
                            return <div className="info" key={i} onClick={() => {
                                window.scrollTo(0, 0);
                                (window as any).stockSearch(f.symbol);
                            }}>
                    <StockLogo symbol={f.symbol} />
                                <div className="follow-info">
                                    <div className='top-info'>
                                        <div className="title-info">
                                            <div className="title">{f.symbol.split("_")[0]}</div>
                                            <div className="subtitle">_{f.symbol.split("_")[1]}</div>
                                        </div>
                                        <div className="data" style={{ color: f.daily_return > 0 ? "#489c1e" : "red" }}>{(f.daily_return * 100).toFixed(2)}%</div>
                                    </div>
                                    <div className='bottom-info'>
                                        <div className="follow-abstract">{f.symbol_name}</div>
                                        <div className="data">{f.daily_change}</div>
                                    </div>
                                </div>
                            </div>
                        })}
                    </div>
                }
            </div>
        </div>
    )} catch(e){
      console.log("build error $e");
    }
    return <div></div>
});