
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Avatar, Spin } from 'antd';
import './CreatorCenter.scss';
import { Sub_Bg_Img } from '../../images/SubBg';
import { creatorCenterStore } from '../../stores/CreatorCenterStore';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { Icon_Play } from '../../images/icons/Play';
import { Icon_Play_List } from '../../images/icons/PlayList';
import { useHistory } from 'react-router';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';

export const CreatorCenterPage = observer(() => {
  useEffect(() => {
    creatorCenterStore.getHotUsers();
    creatorCenterStore.getHotVideos();
    creatorCenterStore.getHotCollections();
  }, []);
  let history = useHistory();
  return (
    <div className="CreatorCenterPage">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title">OLI 创作者社区</div>
          <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div>
        </div>
      </header>
      <div className="contentWrapper">
        <div className="content">
          <div className="hotCreater">
            <div className="top">
              <div className="title">热门创作者</div>
              <div className="more" onClick={() => {
                window.scrollTo(0, 0);
                history.push('/creatorUserList')
              }}>更多</div>
            </div>
            {creatorCenterStore.loadingHotUser ? <div className="loading">
              <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
            </div> : <div className="list">
              {creatorCenterStore.userList.map((user, i) => <div className='item user' key={i} onClick={() => {
                window.scrollTo(0, 0);
                history.push(`/creatorVideos/${user.id}`)

              }}>
                <div className="top-profile">
                  <Avatar src={user?.avatar} />
                  <div className="info">
                    <div className="name">{user?.username}</div>
                    <div className="amount">{user?.followcount}订阅</div>
                  </div>
                </div>
              </div>)}
            </div>}
          </div>

          <div className="hotVideos">
            <div className="top">
              <div className="title">热门视频</div>
              <div className="more" onClick={() => {
                window.scrollTo(0, 0);
                history.push('/creatorVideoList')
              }}>更多</div>
            </div>
            {creatorCenterStore.loadingHotUser ? <div className="loading">
              <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
            </div> : <div className="list">
              {creatorCenterStore.hotVideos.map((item, i) => <div key={i} className="creatorVideoItem" onClick={() => {
                // creatorVideoStore.selectedItem = item;
              }}>
                <div className="thumb">
                  <img src={item.thumb} alt="" />
                </div>
                <p className="name">{item.title}.</p>
                <div className="div-block-41">
                  <div className="view-left">
                    <EyeOutlined style={{ color: '#5F5E61' }} />
                    <div className="viewCount">{0}</div>
                    <StarOutlined style={{ color: '#5F5E61' }} />
                    <HeartOutlined style={{ color: '#5F5E61' }} />
                  </div>
                  <div>
                    <div className="date">{item.createtime.slice(0, 10)}</div>
                  </div>

                </div>
                <div className="mask">
                  <img
                    src={Icon_Play}
                    loading="lazy" alt="" className="image-23" />

                </div>
              </div>)}
            </div>}
          </div>



          <div className="hotCollections">
            <div className="top">
              <div className="title">热门锦集</div>
              <div className="more" onClick={() => {
                window.scrollTo(0, 0);
                history.push('/creatorCollectionList')
              }}>更多</div>
            </div>
            {creatorCenterStore.loadingHotUser ? <div className="loading">
              <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
            </div> : <div className="list">
              {creatorCenterStore.hotCollections.map((item, i) => <div key={i} className="creatorVideoItem" onClick={() => {
                window.scrollTo(0, 0);
                creatorVideoStore.collectionItem = item;
                history.push(`/creatorDetail/${item.id}`)
              }}><a
                href="javascript:;" className="link-block-4 w-inline-block">
                  <div className="thumb">

                    <img src={item.cover} alt="" />

                    <div className="collect-info">
                      <div className="count">{item.videocount}</div>
                      <img src={Icon_Play_List} alt="" />
                    </div>
                  </div>
                  <p className="name">{item.name}.</p>

                </a>

              </div>)}
            </div>}
          </div>
        </div>
      </div>
    </div>
  );
}
)