import { Icon_Index_Stock } from '../../../images/icons/Price';
import { observer } from 'mobx-react';
import './Statistic';
import { Search_Bg_Img } from '../../../images/SearchBg';
import { StockSearchInput } from '../Search/SearchInput';
import StatisticStore from '../../../stores/StatisticStore';
import { useEffect } from 'react';
import { Above, QuotaType } from '../../../services/StockQuotaService';
import ChartsConfig from '../../../config/ChartsConfig';
import { StatisticType } from '../../../services/StatisticService';
import ChartStore from '../../../stores/ChartStore';
import NoData from '../NoData';
import { verifyRule } from '../../../utils/globalFuncs';
import { userStore } from '../../../stores/UserStore';

export const StockQuota = observer(() => {
     var onChange = (type: QuotaType, index: number) => {
        StatisticStore.handleQuotaTabChange(type,index);
    };
    let rules = userStore.userInfo?.rules || [];
     var  subscribed = rules?.indexOf('technicalForm') > -1;

    var list =(StatisticStore.quotaType == QuotaType.above? StatisticStore.quotaAbove : StatisticStore.quotaBelow);
    return (
        <div className="quotaContent" style={{ maxHeight: ChartStore.contentHeight-30 }}>
            <div className="info">
                <div className="word">{StatisticStore.quotaSummery?.symbol?.split('_')[0]}行业类型: {StatisticStore.quotaSummery?.tag}</div>
                <div className="word">有{StatisticStore.quotaSummery?.nb_above}个指标高于行业中位值，{StatisticStore.quotaSummery?.nb_below}个指标低于行业中位值</div>
            </div>

            <div className="statisticTabWrapper">
            <div className="taberContainer">
                <div className="tabBar" style={{
                    width: (100 / ChartsConfig.quotaTabs.length) + '%',
                    left: StatisticStore.quotaPosition
                }}
                ></div>
                <div className="taber">
                    {ChartsConfig.quotaTabs.map((t, i) => {
                        return <div key={i} className={StatisticStore.quotaType === t.type ? 'tabItemChecked' : 'tabItem'} onClick={() => {
                            onChange(t.type, i);
                            return;
                        }}>
                            {t.text}
                        </div>
                    })}
                </div>

            </div>
        </div>


           {!subscribed ? <NoData  text={'订阅后可查看'}/> :list.length == 0? <NoData />: <div className="list">

                {list.map((q:any,i)=>{
                    return <div className="item" key={i}>
                          <div className="title">{q.ch}</div>
                          <div className="progress">
                              <div className="value" style={{width:`${q.rank *100}%`,background:StatisticStore.quotaType == QuotaType.above?"#52CFBF":"#FF505F"}}></div>
                          </div>
                          <div className="bottom">
                              <div>{q.ratio}</div>
                              <div>{(q.rank *100).toFixed(2)}%</div>
                          </div>
                    </div>
                })}
            </div>}
        </div>
    );
})


