import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-awesome-slider/dist/styles.css';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import eventBus from '../../utils/eventBus';
import { Icon_Play } from '../../images/icons/Play';
import IntersectionImage from 'react-intersection-image';
import { verifyRule } from '../../utils/globalFuncs';
import { LocalStorageService } from '../../utils/storage';
import { Sub_Bg_Img } from '../../images/SubBg';
import { creatorCenterStore } from '../../stores/CreatorCenterStore';
import { Input } from 'antd';
import '../Videos/Videos.scss';
import NoData from '../Stock/NoData';
import { CommentPage } from '../Creator/Comment';


const { Search } = Input;

export const CreatorVideoList = observer((props: any) => {
    useEffect(() => {
        creatorCenterStore.getAllVideos();
    }, []);
    eventBus.addListener('toBottom', () => {
        creatorCenterStore.getAllVideos(true);
    })
    const tabs = [{ text: "免费视频", id: 0, isfree: true},
    { text: "付费视频", id: 1, isfree: false}];
    return (
        <div className="videoPage">
            <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
                <div className="headContent">
                    <div className="title">傲利创作者视讯</div>
                    <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div>
                </div>
            </header>
            <div className="videoWrapper">
                <div className="topNav">
                    <div className="menu">
                        {
                            tabs.map((r) => {
                                return <div className="title" style={{ color: creatorCenterStore.isfree == r.isfree ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                                    creatorCenterStore.noMore = false;
                                    creatorCenterStore.isfree = r.isfree;
                                    creatorCenterStore.getAllVideos();
                                }}>
                                    {r.text}
                                </div>
                            })
                        }

                    </div>
                    <div className="search">
                        <Search placeholder="请输入关键词" onChange={(v) => {
                           creatorCenterStore.searchkey = v.target.value;
                        }} onSearch={(v) => {
                            creatorCenterStore.getAllVideos();
                        }} enterButton />
                    </div>
                </div>
                <ul className="content">
                    {creatorCenterStore.loading ? <div className="loading">
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div>   :  creatorCenterStore.videos.length==0? <NoData />: creatorCenterStore.videos.map((item, idx) => {
                        if (!item) {
                            return <li className="item"></li>
                        }
                        let style: string = "collection-item-2 w-dyn-item w-col w-col-4 videoItem";
                        return <li key={idx} className={(idx + 1) % 4 != 0 ? `rightAdjust ${style}` : style} onClick={() => {
                            let isLogin = LocalStorageService.get("token");
                            if (!verifyRule('technicalForm')) {
                                if (item.isfree != 1 && isLogin) {
                                    creatorCenterStore.selectedItem = item;
                                    return;
                                }
                                return;
                            }
                            creatorCenterStore.selectedItem = item;
                        }}>
                            <div className="div-block-39">
                                <IntersectionImage src={item.thumb} className="thumb" />
                                <div className="div-block-40">
                                    <p className="paragraph-8">{item.title}.</p>
                                    <div className="div-block-41">
                                        <div className="view-left">
                                            <EyeOutlined />
                                            <div className="viewCount">{0}</div>
                                            <StarOutlined style={{ color: item.isfollow === 1 ? '#1c2437' : 'inherit' }} />
                                            <HeartOutlined style={{ color: item.islike === 1 ? '#1c2437' : 'inherit' }} />
                                        </div>
                                        <div>
                                            <div className="text-block-9">{item.createtime.slice(0, 10)}</div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                            <div className="mask">
                                <img
                                    src={Icon_Play}
                                    loading="lazy" alt="" className="image-23" />

                            </div>
                        </li>
                        //     return <li className="item" key={idx} onClick={() => {
                        //         creatorCenterStore.selectedItem = item;
                        //     }}>

                        //         <LazyLoadImage
                        //             alt={item.title}
                        //             effect="opacity"
                        //             src={item.thumb} />
                        //         <div className="title">{item.title}</div>
                        //         <div className="bottom">
                        //             <div className="left">
                        //                 <EyeOutlined />
                        //                 <div className="viewCount">{item.viewcount}</div>
                        //                 <StarOutlined style={{ color: item.isfollow === 1 ? '#1c2437' : 'inherit' }} />
                        //                 <HeartOutlined style={{ color: item.islike === 1 ? '#1c2437' : 'inherit' }} />
                        //             </div>
                        //             <div className="date">{item.createtime.split(" ")[0]}</div>
                        //         </div>
                        //     </li>
                    })
                    }
                </ul>
                {creatorCenterStore.loadingMore && <div className="loadmore">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
                </div>}
            </div>
            {
                creatorCenterStore.selectedItem &&
                <div className="videoContent">
                    <div className="tabs">
                        <img className="back" src={Icons.close} alt="" onClick={() => {
                            creatorCenterStore.selectedItem = undefined;
                        }} />

                        {/* <div className="items">
                            <div className="like">
                                <img src={true ? Icons.icon_like_s : Icons.icon_like} alt="" onClick={() => {
                                    // researchDetailStore.like();
                                }} />
                                <div className="count">{22}</div>
                            </div>
                            <img className='follow' src={true ? Icons.follow_s : Icons.follow} alt="" onClick={() => {
                                // researchDetailStore.follow();
                            }} />
                        </div> */}
                    </div>
                    <div className="content">
                        <div className='videoPlayer'>
                            <AwesomeSlider
                                onFirstMount={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.autoplay = true;
                                    video.controlsList = "nodownload";
                                    video.disablePictureInPicture = "true";
                                    video.οncοntextmenu = "return false";
                                }}
                                onTransitionStart={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.controlsList = "nodownload";
                                    video.disablePictureInPicture = "true";
                                    video.οncοntextmenu = "return false";
                                    if (video !== null && !video.paused) {
                                        video.pause();
                                    }
                                }}
                                onTransitionEnd={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.controlsList = "nodownload";
                                    video.οncοntextmenu = "return false";
                                    video.οncοntextmenu = "return false";
                                    video.play();
                                }}
                                bullets={false}
                                selected={creatorCenterStore.videos.map((l) => l.id).indexOf(creatorCenterStore.selectedItem.id)}
                                media={creatorCenterStore.videos.map((item) => { return { source: item.url } })}
                            />
                            <div className="title">
                                {creatorCenterStore.selectedItem.title}
                            </div>
                            <CommentPage objectid={creatorCenterStore.selectedItem.id} objecttype={5}/>
                        </div>
                        <div className="info">
                            <div className="videoList">
                                {creatorCenterStore.videos.map((item, idx) => {
                                    if (!item) {
                                        return <li className="item"></li>
                                    }
                                    return <li className="item" key={idx} onClick={() => {
                                        let isLogin = LocalStorageService.get("token");
                                        if (!verifyRule('technicalForm')) {
                                            if (item.isfree != 1 && isLogin) {
                                                creatorCenterStore.selectedItem = item;
                                                return;
                                            }
                                            return;
                                        }
                                        creatorCenterStore.selectedItem = item;
                                        creatorCenterStore.selectedItem = item;
                                    }}>

                                        <LazyLoadImage
                                            alt={item.title}
                                            effect="opacity"
                                            src={item.thumb} />
                                        <div className="title">{item.title}</div>
                                    </li>
                                })}
                            </div>
                        </div>

                    </div>


                </div>
            }

        </div >
    )
});