import './Publish.scss';
import { useEffect, useState } from 'react';
import { VodAuth, VodService } from '../../services/VodService';
import AliyunUpload from 'aliyun-upload-vod';
import { publishStore, VideoUploadItem } from '../../stores/PublishStore';

export interface UploaderProps {
    item: VideoUploadItem;
}

export const Uploader = (props: UploaderProps) => {
    const [authProgress, setpRrogress] = useState(0);
    const [tips, setTips] = useState('上传中...');
    const [size, setSize] = useState(0);
    var uploader: any;
    useEffect(function () {
        var userData = '{"Vod":{}}'
        uploader = createUploader();
        if(!props.item.added){
            uploader.addFile(props.item.file, null, null, null, userData);
            uploader.startUpload()
        }
    }, []);

    var videoId: string;
    var createUploader = function () {
        let _uploader = new AliyunUpload.Vod({
            timeout: 60000,
            partSize: 1048576,
            parallel: 5,
            retryCount: 3,
            retryDuration: 2,
            region: 'cn-shenzhen',
            userId: '5725013695904696',
            // 添加文件成功
            addFileSuccess: function (uploadInfo: any) {
                console.log("添加成功",uploadInfo)
                console.log("props.checkpoint",uploadInfo.checkpoint)
                props.item.added = true;
                setSize(uploadInfo.checkpoint.fileSize);
            },
            // 开始上传
            onUploadstarted: async function (uploadInfo: any) {
                console.log("onUploadstarted",uploadInfo.checkpoint)
                
                // if (!uploadInfo.videoId) {
                    var auth: VodAuth;
                    auth = await new VodService('测试.mp4').send();
                    var uploadAuth = auth.uploadAuth
                    var uploadAddress = auth.uploadAddress
                    videoId = auth.videoId
                    uploader.setUploadAuthAndAddress(uploadInfo, uploadAuth, uploadAddress, videoId);
                    props.item.id = videoId;
                // } else {
                // }
            },
            // 文件上传成功
            onUploadSucceed: function (uploadInfo: any) {
                setTips('上传成功');
                console.log("uploadInfo sucesss",uploadInfo);
                props.item.url = `https://${uploadInfo.bucket}.oss-cn-shenzhen.aliyuncs.com/${uploadInfo.object}`;
    },
            // 文件上传失败
            onUploadFailed: function (uploadInfo: any, code: any, message: any) {
                setTips('上传失败');
            },
            // 取消文件上传
            onUploadCanceled: function (uploadInfo: any, code: any, message: any) {
            },
            // 文件上传进度，单位：字节, 可以在这个函数中拿到上传进度并显示在页面上
            onUploadProgress: function (uploadInfo: any, totalSize: any, progress: any) {
                console.log("onUploadProgress",uploadInfo.checkpoint)

                // console.log("onUploadProgress:file:" + uploadInfo.file.name + ", fileSize:" + totalSize + ", percent:" + Math.ceil(progress * 100) + "%")
                let progressPercent = Math.ceil(progress * 100)
                setpRrogress(progressPercent);
            },
            // 上传凭证超时
            onUploadTokenExpired: function (uploadInfo: any) {

            },
            // 全部文件上传结束
            onUploadEnd: function (uploadInfo: any) {
                console.log("onUploadEnd: uploaded all the files")
            }

        })
        return _uploader;
    }
    return (
        <div className="uploadItem" >
            <div className="video-upload-status">
                <div className="basic-info">
                    <div className="m-tip-title">
                        <span className="percent">{tips}</span>
                        <span className="tips">{props.item.name}</span>
                        {/* <span className="tips size">大小:{(size / (1024 * 1024)).toFixed(1)}MB</span> */}
                    </div>
                    <div className="m-right-btn">
                        <span className="split-line">
                        </span>
                        <span className="btn" onClick={(e)=>{
                            e.stopPropagation();
                               publishStore.deleteVideo(props.item.name);
                        }}>删除</span>
                    </div>
                </div>
                <div className="upload-progress">
                    <div className="progress-bar-inner" style={{ width: `${authProgress}%` }} >
                    </div>
                </div>
            </div>

        </div>
    )
};