import './Publish.scss';
import { useState } from 'react';
import { CreateCollectService } from '../../services/PublishService';
import { showError, showLoading, showSuccess } from "../../components/Message";
import { publishStore } from '../../stores/PublishStore';
import { Modal } from 'antd';
import { ImageUpload } from './ImageUpload';

interface VideoCollecProps {
    visible: boolean;
    cancle: Function;
}


export const VideoCollectModal = (props: VideoCollecProps) => {
    let name: string = '';
    let price: string = '';
    let cover: string = '';
    let trycount: string = '';
    var creatCollect = async () => {
        showLoading('提交中...');
        //    try{
        await new CreateCollectService({
            name: name,
            price: price,
            cover: cover,
            trycount: trycount,
        }).send();
        //    }catch (e){
        //        showError(`${e}`);
        //        return ;
        //    }
        showSuccess("创建成功");
        publishStore.collectModalVisible=false;
        publishStore.getCollections();
    }

    return (

        <Modal title="视频合集" visible={props.visible}
            width={400}
            footer={null}
            centered={true}
            onCancel={() => { props.cancle() }}
        >
            <div className="video-collection" >
                <div className="byte-row byte-form-layout-horizontal">
                    <div className="byte-form-label-item byte-col byte-col-4 title">
                        <label> 合集标题</label>
                    </div>
                    <div className="byte-form-item-wrapper byte-col input-row">
                        <div className="byte-form-item-control" id="description">
                            <div className="byte-form-item-wrapper-children">
                                <input placeholder="请输入该合集的标题" onChange={(v) => {
                                    name = v.target.value;
                                }} className="xigua-input show-limit byte-input byte-input-size-large" />
                            </div>
                        </div>

                    </div>
                </div>
                <div className="byte-row byte-form-layout-horizontal">
                    <div className="byte-form-label-item byte-col byte-col-4 title">
                        <label> 合集价格</label>
                    </div>
                    <div className="byte-form-item-wrapper byte-col input-row">
                        <div className="byte-form-item-control" id="description">
                            <div className="byte-form-item-wrapper-children">
                                <input placeholder="请输入该合集的价格" onChange={(v) => {
                                    price = v.target.value;
                                }} className="xigua-input show-limit byte-input byte-input-size-large" />
                            </div>
                        </div>

                    </div>
                </div>
                <div className="byte-row byte-form-layout-horizontal">
                    <div className="byte-form-label-item byte-col byte-col-4 title">
                        <label> 试看集数</label>
                    </div>
                    <div className="byte-form-item-wrapper byte-col input-row">
                        <div className="byte-form-item-control" id="description">
                            <div className="byte-form-item-wrapper-children">
                                <input placeholder="请输入该合集的试看集数" onChange={(v) => {
                                    trycount = v.target.value;
                                }} className="xigua-input show-limit byte-input byte-input-size-large" />
                            </div>
                        </div>

                    </div>
                </div>

                <div className="byte-row byte-form-layout-horizontal">
                    <div className="byte-form-label-item byte-col byte-col-4 title">
                        <label> 合集封面</label>
                    </div>
                    <div className="byte-form-item-wrapper byte-col input-row">
                        <div className="byte-form-item-control" id="description">
                            <div className="byte-form-item-wrapper-children">
                                <ImageUpload callback={(url: string) => {
                                    cover = url;
                                }} />
                            </div>
                        </div>

                    </div>
                </div>




                <button className={`publish-button active`} onClick={() => {
                    creatCollect();
                }}><span
                    className="_1Qjd7">创建合集</span></button>
            </div>
        </Modal>


    )
};