import { Charts } from "./Charts/Charts";
import { Search } from "./Search/Search";
import { useEffect } from "react";
import { observer, } from "mobx-react";
import { FullScreenCharts } from "./Charts/FullScreenChart";
import { Statistic } from "./Statistic/Statistic";
import { Recommend } from "./Statistic/Recommend";
import ChartStore from "../../stores/ChartStore";
import { Loading } from "../../components/Loading";
import './StockIndex.scss';
import { registerGlobalFuncs, verifyRule } from "../../utils/globalFuncs";
import { Top_Bg_Img } from "../../images/topBg";
import { userStore } from "../../stores/UserStore";
import StatisticStore from "../../stores/StatisticStore";
import { StockQuota } from "./Statistic/StockQuota";
import eventBus from "../../utils/eventBus";

export const StockIndex = observer((props: any) => {
  let symbol = props.match.params.symbol;
  let strategy = props.match.params.strategy;
  useEffect(() => {
    eventBus.emit('headTransparent');
    registerGlobalFuncs();
    var footer: any = document.getElementsByClassName('footerWrapper')[0];
    if (footer) {
      footer.style.display = 'none';
    }
    if(userStore.userInfo){
      StatisticStore.getFollowed();
    }
    (window as any).stockSearch(symbol,strategy);
    document.body.style.overflowY = "hidden";
    return () => {
    document.body.style.overflowY = "auto";
    var footer: any = document.getElementsByClassName('footerWrapper')[0];
      if (footer) {
        footer.style.display = '';
      }
    }

  }, []);
  if (ChartStore.fullScreen) {
    return <FullScreenCharts />;
  }
  eventBus.addListener('login', (value: boolean) => {
    StatisticStore.getFollowed();
  })
  return (
    <div className="StockIndex section-5 wf-section" >
      <img  className="headerTop" src={Top_Bg_Img} alt="" />
      <div className="StockWrapper">
        <Search />
        <div className='middleContent' style={{ height: ChartStore.contentHeight }}>
          <div className="chartContent">
            <Charts />
          </div>
          <div className="infoContent">
            <div className="infoTabs">
              <div className={`item ${ChartStore.stockTab == 0 ? 'active' : ''}`} onClick={() => {
                ChartStore.stockTab = 0
              }}>财务状况</div>
              <div className={`item ${ChartStore.stockTab == 1 ? 'active' : ''}`} onClick={() => {
                  if(!verifyRule('technicalForm')){
                    return ;
                  }
                ChartStore.stockTab = 1
              }}>推荐评级</div>
            </div>
            {ChartStore.stockTab === 0 ? <Statistic /> : <Recommend />
            }
          </div>
        </div>
        <div className="rightContent">
          <StockQuota />
        </div>
        <div className="loading-content" style={{ display: ChartStore.pending ? 'block' : 'none' }}>
          <Loading />
        </div>
      </div>
    </div>
  );
});
