    public static setStatisticChart = (data1: Array<Array<number>>, data2: Array<Array<number>>) => {
        return {
            chart: {
                events: {
                    load: function () {
                    }
                },
                zoomType: 'x',
                plotBackgroundColor: 0,
                spacing: [5, 0, 5, 5]

            },
            scrollbar: {
                enabled: false
            },
            navigator: {
                enabled: false
            },
            rangeSelector: {
                enabled: false,
            },
            tooltip: {
                enabled: false
            },
            yAxis: [{
                labels: {
                    align: 'right',
                    style: { "fontSize": "18px" },
                    formatter: function (): any {
                        let value: number = (this as any).value;
                        return numFormatter(value);
                    }
                },
                gridLineWidth: 0.5,
                endOnTick: true,
                crosshair: {
                    label: {
                        enabled: true,
                        formatter: function (value:number): any {
                            return numFormatter(value,2);
                        },
                        style: { "fontSize": "18px" },
                    }
                },
                // tickAmount: 7,
                opposite: false,
                resize: {
                    enabled: true
                }
            }],
            xAxis: [{
                labels: {
                    style: { "fontSize": "18px" },
           
                },
                crosshair: {
                    label: {
                        enabled: true,
                        style: { "fontSize": "18px" },
                    }
                },
                startOnTick: true,
                
            }],
            stockTools: {
                gui: {
                    enabled: true
                }
            },

            series: [{
                type: 'line',
                name: '总资产',
                data: data1,
                color: "#a86c24",
                tooltip: {
                    valueDecimals: 2
                }
            }, {
                type: 'line',
                name: '总负债',
                color: "#2476a8",
                data: data2,
                tooltip: {
                    valueDecimals: 2
                }
            }],
        };
    }

