import { Form, Input, Button, Select } from 'antd';
import { UserOutlined, LockOutlined, MailOutlined,PhoneOutlined } from '@ant-design/icons';
import { LoginModel, loginStore, RegisterType } from '../../stores/LoginStore';
import { observer } from 'mobx-react';
import './Login.scss';
import { areaCodes } from './AreaCodes';

const { Option } = Select;


interface registerProps {
    close?: Function;
}


export const RegisterPage = observer((props: registerProps) => {

    var phone="";
    var areacode="86"
    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }} defaultValue="86" onChange={(e)=>{
                areacode=e;
            }}>
                {areaCodes.map((e,i)=><Option key={i} value={e.areacode}>{e.name} (+{e.areacode})</Option>)}
            </Select>
        </Form.Item>
    );


    return (
        <Form
            name="normal_login"
            className="login-form"
            initialValues={{ remember: true }}
            onFinish={(v) => {
                loginStore.register(v, props.close)}}
        >
            <Form.Item
                name="username"
                rules={[{ required: true, message: '请输入姓名' }]}
            >
                <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="请输入姓名" />
            </Form.Item>
            {loginStore.registerType == RegisterType.email?<div>
            <Form.Item
                name="email"
                rules={[{ required: true, message: '请输入邮箱地址' }]}
            >
                <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder="请输入邮箱地址" />
            </Form.Item>
            <Form.Item
                name="password"
                rules={[{ required: true, message: '请输入密码' }]}
            >
                <Input
                    prefix={<LockOutlined className="site-form-item-icon" />}
                    type="password"
                    placeholder="最少8位且须包含大小写字母"
                />
            </Form.Item>
            </div>
            :
           <div>


            <div className="phone-type">
           <Form.Item
                name="phone"
                rules={[{ required: true, message: 'Please input your phone number!' }]}
            >
                <Input onChange={(e) => phone = e.target.value} addonBefore={prefixSelector} placeholder="请输入您的手机号码"/>
            </Form.Item>
</div>
            <div className="captcha phone-type">
                <Form.Item
                    className="codeInput"
                    name="code"
                    rules={[{ required: true, message: '请输入验证码' }]}
                >
                    <Input prefix={<PhoneOutlined  className="site-form-item-icon" />} placeholder="请输入验证码" />
                </Form.Item>
                <Button htmlType="submit" className="forget-form-button" onClick={(e) => { 
                     e.preventDefault();
                     e.stopPropagation();
                    loginStore.sendPhone(phone,1,areacode) }}>
                    {loginStore.sendBtnText}
                </Button>
            </div>

           </div>}

            <Form.Item>
                <Button htmlType="submit" className="login-form-button">
                    注册
                </Button>
                <div className="form-wrapper">
                {loginStore.LoginModel === LoginModel.registerPop?<div className="toLogin">
                    <span>新注册用户享受免费7天订阅</span>
                    </div>:<div className="toLogin"><span>已有账号？</span><span className="loginLabel" onClick={() => {
                        if (loginStore.registerLoading) return;
                        loginStore.setLoginModel(LoginModel.login)
                    }}> 登录</span></div>}    
                </div>
            </Form.Item>
        </Form>
    );
})













