import { ResearchItem } from '../../services/ResearchService';
import { useHistory } from 'react-router';
import { EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import eventBus from '../../utils/eventBus';
import '../Main/Main.scss';
import { goto, verifyRule } from '../../utils/globalFuncs';

interface ReseatchItemProps {
    key: any;
    idx: any;
    item: ResearchItem;
}

function ResearchItemPage(props: ReseatchItemProps) {
    var item: ResearchItem = props.item;
    return (
        <div key={props.key} className="researchItem" onClick={() => {
            verifyRule('researchReport',`/researchDetail/${item.id}`)
        }}>
            <div className="content">
                <div className="titleWrapper">
                <div className="title">{item.title}.</div>
                </div>
        
                <div className="abstractWrapper">
                <p className="abstract">{item.content}.</p>

                </div>
                <div className="date">
                    <div className="color"></div>
                    <div className="time">
                        <div className="top">{item.createtime.slice(0, 10)}</div>
                        <div className="bottom">{item.createtime.slice(11, 16)}</div>
                    </div>
                </div>
                <div className="info">
                    <div className="view-left">
                        <EyeOutlined className="icon-gary" />
                        <div className="viewCount">{item.viewcount}</div>
                        <StarOutlined className="icon-gary" style={{ color: item.isfollow === 1 ? '#1c2437' : 'inherit', marginLeft: '10px', marginRight: '10px' }} />
                        <HeartOutlined className="icon-gary" style={{ color: item.islike === 1 ? '#1c2437' : 'inherit' }} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ResearchItemPage;

