import { Upload, Modal } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import React from 'react';
import { settingStore } from '../../stores/SettingStore';

function getBase64(file: any) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

export interface ImageUploadProps {
    callback: Function;
}

export class ImageUpload extends React.Component<ImageUploadProps, any> {
    state = {
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        loading: false,
        fileList: [
        ],
    };

    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async (file: any) => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
            previewTitle: file.name || file.url.substring(file.url.lastIndexOf('/') + 1),
        });
    }; 

    handleChange = (data: any) => {
        let self = this;
        settingStore.OssUpload(data.file.originFileObj, (url: string) => {
            this.props.callback(url);
    });
    self.setState({ fileList: data.fileList });
};


    render() {
        const { previewVisible, previewImage, fileList, previewTitle, loading } = this.state;
        const uploadButton = (
            <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>Upload</div>
            </div>
        );
        const beforeUpload = (file: any) => {
            return file;
        }
        return (
            <>
                <Upload
                    listType="picture-card"
                    fileList={fileList}
                    beforeUpload={beforeUpload}
                    onPreview={this.handlePreview}
                    onChange={this.handleChange}
                >
                    {fileList.length >= 1 ? null : uploadButton}
                </Upload>
                <Modal
                    visible={previewVisible}
                    title={previewTitle}
                    footer={null}
                    onCancel={this.handleCancel}
                >
                    <img alt="example" style={{ width: '100%' }} src={previewImage} />
                </Modal>
            </>
        );
    }
}