import './Research.scss';
import { researchStore } from '../../stores/ResearchStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Input, Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import ResearchItemPage from './ResearchItem';
import eventBus from '../../utils/eventBus';
import { ResearchNav } from './ResearchNav';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Sub_ListBg_Img } from '../../images/SubListBg';
import NoData from '../Stock/NoData';


const { Search } = Input;
export const Research = observer(() => {
    useEffect(() => {
        researchStore.getData();
    }, []);
    let list: any = researchStore.list;
    let left = list.length % 4;
    for (let i = 0; i < left; i++) {
        list.push(undefined);
    }
    eventBus.addListener('toBottom', () => {
        researchStore.getData(true);
    })
    return (
        <div className="researchWrapper">
            <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
                   <div className="headContent">
                   <div className="title">傲利研报</div>
                   <div className="subTitle">为广大投资者提供对澳洲资本市场的独到见解、深度分析</div>

                   </div>
            </header>
            <div className="contentWrapper"  >
                <ResearchNav />
                <ul className="researchContent">
                    {researchStore.loading ? <div className="loading">
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div>:researchStore.categories.length!=0&&researchStore.list.length==0?<NoData  /> : researchStore.list.map((item, idx) => {
                        if (!item) {
                            return <li className="item"></li>
                        }
                        return <ResearchItemPage item={item} key={idx} idx={idx}/>
                    })
                    }
                </ul>
                {researchStore.loadingMore && <div className="loadmore">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
                </div>}
            </div>

        </div>
    )
});