
import './CreatorVideos.scss';
import { observer } from 'mobx-react';
import { Input } from 'antd';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import { Icon_Search } from '../../images/icons/Search';
import { Icon_Search_Blue } from '../../images/icons/SearchBlue';


const { Search } = Input;
export const VideoNav = observer(() => {
    return <div className="topNav">
        <div className="menu">
            {
                creatorVideoStore.categories.map((r) => {
                    return <div className="title" style={{ borderBottom: creatorVideoStore.type === r.key ? '2px solid #143755' : 'none' }} onClick={() => {
                        creatorVideoStore.type = r.key;
                        creatorVideoStore.resetData();
                    }}>
                        {r.text}
                    </div>
                })
            }
        </div>
        <div className='search-wrapper'>
            <div className="icon-wrapper" onClick={() => {
                if (creatorVideoStore.type == 'video') {
                    creatorVideoStore.getData()
                } else if (creatorVideoStore.type == 'collect') {
                    creatorVideoStore.getCollections();
                }
            }}>
                <img src={Icon_Search_Blue} alt="" className="icon" />
            </div>
            <input id="stockSearchInput" type="text" placeholder='输入关键词搜索视频/专栏' onSubmit={(v) => {
                if (creatorVideoStore.type == 'video') {
                    creatorVideoStore.getData()
                } else if (creatorVideoStore.type == 'collect') {
                    creatorVideoStore.getCollections();
                }
            }}

                onChange={(v) => {
                    creatorVideoStore.searchkey = v.target.value;
                }} />
        </div>
    </div>
})