import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import eventBus from '../../utils/eventBus';
import { Sub_Bg_Img } from '../../images/SubBg';
import { creatorCenterStore } from '../../stores/CreatorCenterStore';
import { Input } from 'antd';
import NoData from '../Stock/NoData';
import { Icon_Play_List } from '../../images/icons/PlayList';
import '../Videos/Videos.scss';
import './CreatorCenter.scss'
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import { useHistory } from 'react-router';

const { Search } = Input;

export const CreatorCollectionList = observer((props: any) => {
    useEffect(() => {
        creatorCenterStore.getAllCollection();
    }, []);
    eventBus.addListener('toBottom', () => {
        creatorCenterStore.getAllCollection(true);
    })
    const tabs = [{ text: "免费锦集", id: 0, isfree: true},
    { text: "付费锦集", id: 1, isfree: false}];
  let history = useHistory();
  return (
        <div className="videoPage">
            <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
                <div className="headContent">
                    <div className="title">傲利创作者锦集</div>
                    <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div>
                </div>
            </header>
            <div className="videoWrapper">
                <div className="topNav">
                    <div className="menu">
                        {
                            tabs.map((r) => {
                                return <div className="title" style={{ color: creatorCenterStore.isfree === r.isfree ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                                    creatorCenterStore.noMore = false;
                                    creatorCenterStore.isfree = r.isfree;
                                    creatorCenterStore.getAllCollection();
                                }}>
                                    {r.text}
                                </div>
                            })
                        }

                    </div>
                    <div className="search">
                        <Search placeholder="请输入关键词" onChange={(v) => {
                           creatorCenterStore.searchkey = v.target.value;
                        }} onSearch={(v) => {
                            var researchContent: any = document.getElementsByClassName('researchContent')[0];
                         creatorCenterStore.getAllCollection();
                        }} enterButton />
                    </div>
                </div>
                <ul className="content">
                    {creatorCenterStore.loading ? <div className="loading">
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div>   :  creatorCenterStore.collections.length==0? <NoData />: creatorCenterStore.collections.map((item, idx) => {
                       
                        return <div key={idx} className="creatorVideoItem" onClick={() => {
                            window.scrollTo(0,0);
                            window.scrollTo(0, 0);
                            creatorVideoStore.collectionItem = item;
                            history.push(`/creatorDetail/${item.id}`)
                        }}><a
                            href="javascript:;" className="link-block-4 w-inline-block">
                                <div className="thumb">
            
                                <img src={item.cover} alt="" />
            
                                <div className="collect-info">
                                            <div className="count">{item.videocount}</div>
                                            <img src={Icon_Play_List} alt="" />
                                        </div>
                                </div>
                                        <p className="name">{item.name}.</p>
                                     
                            </a>
                           
                        </div>
                      
                    })
                    }
                </ul>
                {creatorCenterStore.loadingMore && <div className="loadmore">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
                </div>}
            </div>
        </div >
    )
});