import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import StatisticStore from '../../../stores/StatisticStore';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './WatchList.scss';
import { Movements } from '../../../services/WatchlistService';
import { useHistory } from 'react-router';
import { Index_Market_Img } from '../../../images/IndexMarket';
import { Index_Bg_Img } from '../../../images/IndexBg';
import { StockSearchInput } from '../Search/SearchInput';
import SwapLeftOutlined from '@ant-design/icons/lib/icons/SwapLeftOutlined';
import SwapRightOutlined from '@ant-design/icons/lib/icons/SwapRightOutlined';
import { Icon_Fly } from '../../../images/icons/Fly';
import { StockLogo } from '../StockLogo';



export const WatchListPage = observer(() => {
    let history = useHistory();
    const [index, setIndex] = useState(0);
    useEffect(() => {
        StatisticStore.getWatchMarketlist();
    }, []);
    const tabs = ['涨幅榜', '跌幅榜'];
    var list: Movements[] = (index == 0 ? StatisticStore.markets?.top_movements?.top_10 : StatisticStore.markets?.top_movements?.last_10) || [];
    return (
        <div className="watchListPage">
            <div className="top"  >
                <img src={Index_Bg_Img} alt="" className="bg" />
                <div className="info">
                    {/* <h1 className="topTitle">ANALYSE SMART</h1>
                    <h1 className="subTitle">DREAM BIG.</h1> */}
                    <StockSearchInput
                        blue={true}
                        // TODO: 图标等待替换
                        searchBtn={<a className="searchBtn">
                            <img src={Icon_Fly} />
                        </a>}
                    />
                    <div className="guide">
                        <img src={Index_Market_Img} alt="" className="icon" />
                        <div className="abstract">
                            <div className="title">Oli 行情</div>
                            <div className="details">多元化的视野，专业的知识储备，对市场交易进行深度分析，掌握市场风向，将金融技术和金融服务结合. </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="contentWrapper" >
                {/* <div className="menu">
                    {
                        tabs.map((t, i) => {
                            return <div className="title" key={i} style={{ color: index == i ? '#487bea' : 'black' }} onClick={() => {
                                setIndex(i);
                            }}>
                                {t}
                            </div>
                        })
                    }
                </div> */}
                <div className="deal-date">上个交易日: {StatisticStore.markets?.top_movements.date}</div>
                <ul className="watchListContent">
                    {StatisticStore.marketLoading ? <div className="loading" style={{ width: '100%', height: '500px' }}>
                        <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                    </div> : list.map((item, idx) => {
                        return <div className="item" key={idx} onClick={() => {
                            window.scrollTo(0, 0);
                            history.push(`/stockDetail/${item.symbol_id}/stock`);
                        }}>
                            <div className="info">
                                {/* <div className="icon">{item.symbol_id[0]}</div> */}
                                {/* <img className="icon" src={`http://aolinews.oss-ap-southeast-2.aliyuncs.com/logos/${item.symbol_id}.png`} alt="" /> */}
                              <StockLogo  symbol={item.symbol_id}/>
                              
                                <div className="text">
                                    <div className="symbol">{item.symbol_id}</div>
                                    <div className="name">{item.symbol_name}</div>
                                </div>
                            </div>
                            <div className="price">
                                <div className="left">{item.price}</div>
                                <div className="right" style={{ color: index == 0 ? '#D0021B' : '#6bad10' }}>{(item.daily_return * 100).toFixed(2)}%</div>
                            </div>
                        </div>
                    })
                    }
                  {!StatisticStore.marketLoading && (
                    <a onClick={()=>{
                        setIndex(0);
                    }}>
                        <SwapLeftOutlined className={`arrowLeft ${index == 0 ? '' : 'active'}`} />
                    </a>
                  )}

                  {!StatisticStore.marketLoading && (
                    <a onClick={()=>{
                        setIndex(1);
                    }}>
                        <SwapRightOutlined className={`arrowRight ${index == 1 ? '' : 'active'}`} />
                    </a>
                  )}

                </ul>
            </div>
            <div className="line" />
        </div >
    )
});