import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { DatePicker, Input, Spin } from 'antd';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Sub_ListBg_Img } from '../../images/SubListBg';
import Calendar from 'react-calendar';
import { LoadingOutlined } from '@ant-design/icons';
import 'react-calendar/dist/Calendar.css';
import { marketCalendarStore } from '../../stores/MarketCalendarStore';
import { CalendarItem } from '../../services/StockCalendar';
import './MarketCalendar.scss';
import zh_CN from "antd/lib/locale-provider/zh_CN";
import { ConfigProvider } from 'antd';
import NoData from '../Stock/NoData';

const { RangePicker } = DatePicker;


const { Search } = Input;
export const MarketCalendar = observer(() => {
  useEffect(() => {
    marketCalendarStore.changeType(1);
  }, []);
  const tebs = [{ text: "昨天", id: 0 },
  { text: "今天", id: 1 },
  { text: "明天", id: 2 },
  { text: "本周", id: 3 },
  { text: "下周", id: 4 },]
  return (
    <div className="calendarWrapper">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title" style={{textAlign:"center"}}>财经日历</div>
          <div className="subTitle">为广大投资者提供对澳洲资本市场的独到见解、深度分析</div>

        </div>
      </header>
      <div className="calendarContentWrapper">
        <div className="topNav">
          <div className="menu">
            {
              tebs.map((r) => {
                return <div className="title" style={{ color: marketCalendarStore.type === r.id ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                  // window.scrollTo(0, 0);
                  marketCalendarStore.changeType(r.id);
                }}>
                  {r.text}
                </div>
              })
            }

          </div>
          <div className="datePicker">
            <ConfigProvider locale={zh_CN}>
              <RangePicker onChange={(c) => {
                marketCalendarStore.setRange(c);
              }} />
            </ConfigProvider>
          </div>
        </div>

        {marketCalendarStore.loading ? <div className="loading" >
          <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
        </div> :marketCalendarStore.list.length==0?  <div>
        <NoData />
        </div>
:        
        <div>
          <div className="calendarTitle">
            <div className="time">
              时间
            </div>
            <div className="country">货币</div>
            <div className="impact">重要性</div>
            <div className="event">活动</div>
            <div className="actual">今值</div>
            <div className="estimate">预测值</div>
            <div className="prev">前值</div>
          </div>
          <div className="calendarContent">
            {marketCalendarStore.list?.map((item: CalendarItem, i) => {
              // if (!item.rating || !item.target_price) {
              //     return <div key={i}></div>
              // }
              return <div className="calendarItem" key={i}>
                <div className="time">{item.time.slice(11, 16)}</div>
                <div className="country">{item.country}</div>
                <div className="impact">{item.impact}</div>
                <div className="event">
                  {item.event}
                </div>
                <div className="actual">
                  {item.actual}
                </div>
                <div className="estimate">
                  {item.estimate}
                </div>
                <div className="prev">{item.prev}</div>
              </div>
            })}
          </div>
        </div>}

      </div>
    </div>
  )
});