import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-awesome-slider/dist/styles.css';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import eventBus from '../../utils/eventBus';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import { Icon_Add } from '../../images/icons/Add';
import { Top_Bg_Img } from '../../images/topBg';
import Avatar from 'antd/lib/avatar/avatar';
import './CreatorVideos.scss';
import { VideoNav } from './VideoNav';
import { VideoListPage } from './VideoList';
import { VideoCollectPage } from './VideoCollect';
import { SubscribeListPage } from './SubscribeList';
import { CommentPage } from '../Creator/Comment';


export const CreatorVideos = observer((props: any) => {
    let id = props.match.params.id;
    useEffect(() => {
        eventBus.emit('headTransparent');
        creatorVideoStore.userId = id;
        creatorVideoStore.getUser();
    }, []);

    // eventBus.addListener('toBottom', () => {
    //     creatorVideoStore.getData(true);
    // })
    let isFollowed = creatorVideoStore.followIds.indexOf(creatorVideoStore.userId) > -1;
    return (
        <div className="creatorVideoPage">
            <img className="headerTop" src={Top_Bg_Img} alt="" />
            {creatorVideoStore.loadingUser ? <div className="mainLoading" >
                <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
            </div> : <div>
                <div className="headContent">
                    <div className="top">
                        <div className="top-profile">
                            <Avatar src={creatorVideoStore.user?.avatar} />
                            <div className="info">
                                <div className="name">{creatorVideoStore.user?.username}</div>
                                <div className="amount">{creatorVideoStore.user?.followcount}订阅</div>
                            </div>
                        </div>
                        {!creatorVideoStore.isSelf && <div className="subscribe-btn" onClick={() => {
                            creatorVideoStore.subscribe(id);
                        }}>
                            {!isFollowed && <img src={Icon_Add} alt="" />}
                            <div className="text">{isFollowed ? '取消' : ''}订阅</div>
                        </div>}
                    </div>
                    < VideoNav />
                </div>
                <div >
                    {creatorVideoStore.type == "introduce" ? <div className="introduce">
                        <div>说明</div>
                        <div>{creatorVideoStore.user?.describe} </div>
                    </div> : creatorVideoStore.type == "video" ? <VideoListPage /> : creatorVideoStore.type == "collect" ? <VideoCollectPage /> : <SubscribeListPage />}
                </div>
            </div>}

            {
                creatorVideoStore.selectedItem &&
                <div className="videoContent">
                    <div className="tabs">
                        <img className="back" src={Icons.close} alt="" onClick={() => {
                            creatorVideoStore.selectedItem = undefined;
                        }} />
                    </div>
                    <div className="content">
                        <div className='videoPlayer'>
                            <AwesomeSlider
                                onFirstMount={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.autoplay = true;
                                    video.controlsList = "nodownload";
                                    video.disablePictureInPicture = "true";
                                    video.οncοntextmenu = "return false";
                                }}
                                onTransitionStart={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.controlsList = "nodownload";
                                    video.disablePictureInPicture = "true";
                                    video.οncοntextmenu = "return false";
                                    if (video !== null && !video.paused) {
                                        video.pause();
                                    }
                                }}
                                onTransitionEnd={(e: any) => {
                                    var video = e.currentSlide.querySelector("video");
                                    video.controlsList = "nodownload";
                                    video.οncοntextmenu = "return false";
                                    video.οncοntextmenu = "return false";
                                    video.play();
                                }}
                                bullets={false}
                                selected={creatorVideoStore.list.filter((l) => l.lookauth ==1).map((l) => l.id).indexOf(creatorVideoStore.selectedItem.id)}
                                media={creatorVideoStore.list.filter((l) => l.lookauth ==1).map((item) => { return { source: item.url } })}
                            />
                            <div className="title">
                                {creatorVideoStore.selectedItem.title}
                            </div>

                            <CommentPage objectid={creatorVideoStore.selectedItem.id} objecttype={5}/>
                        </div>
                        <div className="info">
                            <div className="videoList">
                                {creatorVideoStore.list.filter((l) => l.lookauth ==1).map((item, idx) => {
                                    if (!item) {
                                        return <li className="item"></li>
                                    }
                                    return <li className="item" key={idx} onClick={() => {
                                        creatorVideoStore.selectedItem = item;
                                    }}>

                                        <LazyLoadImage
                                            alt={item.title}
                                            effect="opacity"
                                            src={item.thumb} />
                                        <div className="title">{item.title}</div>
                                    </li>
                                })}
                            </div>
                        </div>

                    </div>


                </div>
            }



        </div >
    )
});