
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { newsStore } from '../../stores/NewsStore';
import eventBus from '../../utils/eventBus';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './News.scss';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Sub_ListBg_Img } from '../../images/SubListBg';

export const NewsPage = observer(() => {
  useEffect(() => {
    newsStore.getData();
  }, [])
  let colorsList = [
    '#fce2c9',
    '#e0e8b5',
    '#f6e2eb',
    '#fef7af',
    '#ffc5d4',
    '#a3d3dd',
    '#f7e9c4',
    '#afd3eb',
  ];
  eventBus.addListener('toBottom', () => {
    newsStore.getData(true);
  })
  return (
    <div className="NewsPage">
      <header className="headBg" style={{ backgroundImage: `url('${Sub_Bg_Img}')`, backgroundPositionY: '-80px', backgroundSize: '100%' }}>
        <div className="headContent">
          <div className="title">傲利快讯</div>
          <div className="subTitle">全球金融市场的每日独家观点及意见，针对多个热门话题、热门产业进行全面的研究与深入的剖析.</div>
        </div>
      </header>

      <div className="contentWrapper">
        <div className="newsContent">
          {newsStore.loading ? <div className="loading">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
          </div> : newsStore.newsList.map((n, i) => <div key={i} className="item">
            <div className="left">
              <div className="title">{n.title}.</div>
              <div className="tit-pic">
               {n.thumb && <img src={n.thumb} />
              }              </div>
            </div>
            <div className="content" dangerouslySetInnerHTML={{ __html: n.content.replace(/\n/g, "<br/>") }}></div>
            <div className="date">
              <div className="color">
              </div>
              <div className="time">
                <div className="top">{n.createtime.slice(0, 10)}</div>
                <div className="bottom">{n.createtime.slice(11, 16)}</div>
              </div>
            </div>
          </div>)}

          {newsStore.loadingMore && <div className="loadmore">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
          </div>}
        </div>
      </div>
      <div className="line" />
    </div>
  );
}
)