import { observer } from 'mobx-react';
import StatisticStore from '../../../stores/StatisticStore';
import { Icon_Search } from '../../../images/icons/Search';
import { useHistory } from 'react-router';
import '../StockIndex.scss';
import eventBus from '../../../utils/eventBus';
import { Icon_Search_Blue } from '../../../images/icons/SearchBlue';

export interface SearchInputProps{
    blue: boolean;
    searchBtn?: any;
}

export const StockSearchInput = observer((props:SearchInputProps) => {
    let history = useHistory();
    return (
        <div className='search-wrapper'>
            <div className="icon-wrapper" onClick={() => {
                if (StatisticStore.symbols.length > 0) {
                    var stockSearchInput:any  = document.getElementById("stockSearchInput")
                    stockSearchInput.value = "";
                    if (window.location.pathname.indexOf("stockDetail") > -1) {
                        
                        (window as any).stockSearch(StatisticStore.symbols[0].symbol);
                        StatisticStore.symbols = [];
                    } else {
                        history.push(`/stockDetail/${StatisticStore.symbols[0].symbol}/stock`);
                        StatisticStore.symbols = [];
                    }

                }
            }}>
                <img src={!props.blue?Icon_Search:Icon_Search_Blue} alt="" className="icon" />
            </div>
            <input id="stockSearchInput" type="text" placeholder='股票代码，如APT或Afterpay' onSubmit={(v) => {
                if (StatisticStore.symbols.length > 0) {
                    var stockSearchInput:any  = document.getElementById("stockSearchInput")
                    stockSearchInput.value = "";
                    window.scrollTo(0, 0);
                    if (window.location.href.indexOf("stockDetail") > -1) {
                        (window as any).stockSearch(StatisticStore.symbols[0].symbol);
                        StatisticStore.symbols = [];
                    } else {
                        history.push(`/stockDetail/${StatisticStore.symbols[0].symbol}/stock`);
                        StatisticStore.symbols = [];
                    }
                    
                }
            }}

                onChange={(v) => StatisticStore.onSearchInputChange(v.target.value)} />
                {props.searchBtn || <div className="go">
                    GO
                </div>}
            <div className="list">
                {StatisticStore.symbols.map((s, i) => <div key={i} className="item" onClick={() => {
                    if (window.location.href.indexOf("stockDetail") > -1) {
                        (window as any).stockSearch(s.symbol);
                        StatisticStore.symbols = [];
                    } else {
                        StatisticStore.symbols = [];
                        history.push(`/stockDetail/${s.symbol}/stock`);
                    }

                    var stockSearchInput:any  = document.getElementById("stockSearchInput")
                    stockSearchInput.value =s.symbol;
                }}>{s.symbol} ({s.name})</div>)}

            </div>
        </div>

    );
})


