
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, } from '@ant-design/icons';
import 'react-awesome-slider/dist/styles.css';
import { creatorVideoStore } from '../../stores/CreatorVideoStore';
import { Icon_Play_List } from '../../images/icons/PlayList';
import { CollectionDetailPage } from './CollectionDetaill';
import { useHistory } from 'react-router';
import NoData from '../Stock/NoData';
import './CreatorVideos.scss';
import { EditCollectionModal } from './EditCollection';
import { CollectionItem } from '../../services/CollectionListService';

export const VideoCollectPage = observer(() => {
    useEffect(() => {
        creatorVideoStore.getCollections();
        creatorVideoStore.getCollectCount();
    }, []);
    var isCharge: boolean = !creatorVideoStore.isfree;
    let history = useHistory();
    return <div className="videoWrapper">
        {!creatorVideoStore.collectionItem && <div className="price-tabs">
            <div className="item" style={{ backgroundColor: isCharge ? '#3272a2' : '#ffffff', color: isCharge ? '#ffffff' : '#3272a2' }} onClick={() => {
                creatorVideoStore.isfree = false;
                creatorVideoStore.getCollections();
            }}>
                <img src="" alt="" />
                <div className="icon" style={{ borderColor: !isCharge ? '#3272a2' : '#ffffff' }}>￥</div>
                <div className="text">付费合集</div>
                <div className="amount">{creatorVideoStore.collectCount?.payCount || '0'}</div>
            </div>
            <div className="item" style={{ backgroundColor: !isCharge ? '#3272a2' : '#ffffff', color: !isCharge ? '#ffffff' : '#3272a2' }} onClick={() => {
                creatorVideoStore.isfree = true;
                creatorVideoStore.getCollections();
            }}>
                <img src="" alt="" />
                <div className="icon" style={{ borderColor: isCharge ? '#3272a2' : '#ffffff' }}>免</div>
                <div className="text">免费合集</div>
                <div className="amount">{creatorVideoStore.collectCount?.freeCount || '0'}</div>
            </div>
        </div>}
        <div>
            {creatorVideoStore.isSelf && <div className="editWrapper">
                <div className="edit" onClick={() => { creatorVideoStore.isEditing = !creatorVideoStore.isEditing }}>编辑</div>
            </div>}
            {creatorVideoStore.collectionItem ? <CollectionDetailPage /> : <ul className="collectionDetailContent">
                {creatorVideoStore.loadingCollection ? <div className="loading">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                </div> : creatorVideoStore.collections.length == 0 ? <NoData /> : creatorVideoStore.collections.map((item, idx) => {
                    if (!item) {
                        return <li className="item"></li>
                    }
                    return <li key={idx} role="listitem" className="videoItem" onClick={() => {
                        window.scrollTo(0, 0);
                        creatorVideoStore.collectionItem = item;
                        history.push(`/creatorDetail/${item.id}`)
                    }}><a
                        href="javascript:;" className="link-block-4 w-inline-block">
                            {creatorVideoStore.isEditing && <div className="tools">
                                <div className="edit" onClick={(e) => {
                                    e.stopPropagation()
                                    creatorVideoStore.editCollectionId = item.id;
                                }}>修改</div>
                                <div className="delete" onClick={(e) => {
                                    e.stopPropagation()
                                    creatorVideoStore.deleteCollection({ id: item.id }, () => {
                                        let list: CollectionItem[] = [];
                                        creatorVideoStore.collections.forEach((l) => {
                                            if (l.id != item.id) {
                                                list.push(l)
                                            }
                                        })
                                        creatorVideoStore.collections = list;
                                    });
                                }}>删除</div>
                            </div>}
                            <div className="thumb">

                                <img src={item.cover} alt="" />

                                <div className="collect-info">
                                    <div className="count">{item.videocount}</div>
                                    <img src={Icon_Play_List} alt="" />
                                </div>
                            </div>
                            <p className="name">{item.name}.</p>

                        </a>

                    </li>
                })
                }
            </ul>}
        </div>
        <EditCollectionModal visible={creatorVideoStore.editCollectionId != undefined} cancle={(data: any) => {
            creatorVideoStore.editCollectionId = undefined
            if (data) {
                let list: CollectionItem[] = [];
                creatorVideoStore.collections.forEach((l) => {
                    if (l.id == data.id) {
                        if (data.name) {
                            l.name = data.name
                        }
                        if (data.cover) {
                            l.cover = data.cover;
                        }
                    }
                    list.push(l)
                })

                creatorVideoStore.collections = list;
            }
        }} />
        {creatorVideoStore.loadingMore && <div className="loadmore">
            <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
        </div>}
    </div>
})