import { Headers } from './Header/Header';
import Routes from '../components/Routes';
import { useEffect } from 'react';
import Footers from './Footer/Footer';
import { researchStore } from '../stores/ResearchStore';
import '../scss/index.scss';
import { userStore } from '../stores/UserStore';
import eventBus from '../utils/eventBus';
import { BackTop } from 'antd';
import { registerGlobalFuncs } from '../utils/globalFuncs';
import { observer } from 'mobx-react';

import StatisticStore from '../stores/StatisticStore';
import { Register_Icon } from '../images/icons/Register';
import { LoginModel, loginStore } from '../stores/LoginStore';
import { RegisterPage } from './Login/Register';
import Icons from '../images/images';
import { CloseOutlined } from '@ant-design/icons';
import Modal from 'antd/lib/modal/Modal';
import { Example } from './Main/Test';
import { creatorVideoStore } from '../stores/CreatorVideoStore';

interface IndexProps {
  routes: any;
}
export const Index = observer((props: IndexProps) => {
  const { routes } = props;
  useEffect(() => {
    registerGlobalFuncs();
    userStore.init();
    StatisticStore.getWatchSymbolist();
    if (userStore.userInfo) {
      StatisticStore.getFollowed();
    }
    researchStore.getLatest();
    researchStore.getCategory();
    creatorVideoStore.getSubscribeList()
    eventBus.addListener('logout', () => {
      userStore.logout();
    });
    let isIndex:boolean = window.location.hash == '#/';
    if(isIndex && !userStore.userInfo){
      userStore.showRegisterPop = true;
    }
  }, []);
  return (
    <div className="page-wrapper">
      <Headers />
      <div className="content">
        <Routes routes={routes} />
      </div>
      <Footers />
      <BackTop />
     <Modal className="register-modal" title={''} visible={userStore.showRegisterPop}
            width={400}
            footer={null}
            centered={false}
            mask ={false}
            maskClosable={false}
            onCancel={() => { userStore.showRegisterPop=false}}
        >
            <div className="top">
          <div className="tips">7天限免 立即享受！</div>
          {/* <CloseOutlined style={{ fontSize: '24px' }} onClick={()=>{
            userStore.showRegisterPop = false;
          }}/> */}
        </div>
        <RegisterPage close={() => {
            userStore.showRegisterPopLoop();
        }} />
        </Modal>
      {(userStore.userInfo || userStore.hideRegisterPopBtn)? <div></div> : <div className="register-pop" onClick={() => {
                   userStore.showRegisterPop = true;

      }}>
        <img src={Register_Icon} alt="" />
      </div>}
    </div>
  );
});

export default Index;
